<template>
  <div class="standart-tab directorys-hardware__main pa-2" v-loading="loading">
    <vd-standart-tab-toolbar @btn-click="handlerBtnClick" v-model="filterText"></vd-standart-tab-toolbar>
    <vd-standart-tab-table ref="refComponent" :filter-text="filterText" :main="toTree" :data="$vdesk.filterTree(filterText,treeData,toTree.folders)" ></vd-standart-tab-table>

    <v-dialog v-model="dialogVisible" max-width="500px" :overlay="true" scrollable>
      <v-card tile>
        <vd-standart-tab-dialog-toolbar v-model="dialogVisible" @btn-click="saveDataAndClose"></vd-standart-tab-dialog-toolbar>
        <v-card-text class="h-100">
          <table class="w-100 align-right" style="border-spacing:8px;">
            <tr>
              <td class="title">:</td>
              <td>
                <el-input :error="selectedDataValidationErrors.name" placeholder=" " v-model="selectedData.name"></el-input>
              </td>
            </tr>
            <tr>
              <td class="title">:</td>
              <td>
                <el-select filterable size="medium" v-model="selectedData._parentId" placeholder=" " class="w-100">
                  <el-option v-for="item in hardwaresections" :key="item.id" :label="item.name" :value="item.id"></el-option>
                </el-select>
              </td>
            </tr>
            <tr>
              <td class="title">:</td>
              <td  class="align-left">
                <el-switch v-model="selectedData.isItem" active-text="" inactive-text=""></el-switch>
              </td>
            </tr>
          </table>
        </v-card-text>        
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "directorys-hardware",
  props:{
    window:{
      type:Boolean,
      default:true
    },
    componentId:String
  },
  data() {
    return {
      toTree:{
        defSort:{prop: 'name', order: 'descending'},
        headers:false,
        key:'id',
        folders:['name'],
        columns:[
          {
            prop:'name',
            label:'',
            tree:true,
            render: (scope) => scope.row.isItem ? scope.row.name : `${scope.row.name}(${scope.row.children.length})`
          }
        ]
      },
      filterText: '',
      dialogVisible:false,
      selectedDataValidationErrors:{
        name:false,
      },
      type:'new',
      selectedData:{
        name:'',
        _parentId:null,
        isItem:true
      },
      loading: false
    };
  },
  computed: {
    ...mapState({
      treeData: state => state.directorys.hardware,
      theme: state => state.worker_data.workerData.theme,
      color: state => state.worker_data.workerData.color
    }),
    hardwaresections(){
      return this.$store.getters.getHardwareDirectorys
    }
  },
  methods: {
    handlerBtnClick(type){
      switch(type){
        case 'add':
          this.handlerAddClick()
          break;
        case 'edit':
          this.handlerEditClick()
          break;
        case 'delete':
          this.handlerDeleteClick()
          break;
      }
    },
    handlerDeleteClick(){
      let node = this.$refs.refComponent.$children[0].$children[0].store.states.currentRow;
      if(node){
        this.$confirm(`    - ${node.name}. ?`, 'Warning', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.selectedData = {
            id:node.id,
            name:node.name,
            _parentId:node._parentId,
            isItem:!!node.isItem
          };
          this.type="delete"
          this.$vdesk.sendUpdate(this)
          this.$message({type: 'success',message: ' '});
        }).catch(() => {
          this.$message({type: 'info',message: ' '});
        });
      }      
    },
    handlerEditClick(){
      let node = this.$refs.refComponent.$children[0].$children[0].store.states.currentRow;
      if(node){
        this.selectedData = {
          id:node.id,
          name:node.name,
          _parentId:node._parentId,
          isItem:!!node.isItem
        };
        this.type="update"
        this.dialogVisible=true
      }      
    },
    handlerAddClick() {
      let node = this.$refs.refComponent.$children[0].$children[0].store.states.currentRow;
      this.selectedData = {
        name:'',
        _parentId:node && !node.isItem ? node.id : null,
        isItem:true
      };
      this.type="new"
      this.dialogVisible = true
    },
    saveDataAndClose() {
      if(!this.selectedData.name){
        this.selectedDataValidationErrors.name=true;
        setTimeout(()=>{this.selectedDataValidationErrors.name=false},2000)
      } else {
        this.$vdesk.sendUpdate(this)
      }
    }
  }
};
</script>

<style>

</style>


