<template>
  <v-card class="standart-tab__card">
    <el-table  class="inner" :current-row-key="main.key" :default-sort="main.defSort" :show-header="main.headers" size="mini" highlight-current-row :data="data" @row-dblclick="handlerRowDblClick">
      <template v-for="column in main.columns">
        <el-table-tree-column v-if="column.tree" :key="column.prop" :column-key="main.key" :prop="column.prop" :label="column.label" :width="column.width || 'auto'" file-icon="mdi mdi-24px mdi-file-document" folder-icon="mdi mdi-24px mdi-folder" parent-key="_parentId" show-overflow-tooltip>
          <template slot-scope="scope">
            {{ column.render ? column.render(scope) : scope.row[column.prop] }}
          </template>
        </el-table-tree-column>
        <el-table-column v-else  :key="column.prop" :prop="column.prop" :label="column.label" :width="column.width" :show-overflow-tooltip="!column.tooltipOff">
          <template slot-scope="scope">
            {{ column.render ? column.render(scope) : scope.row[column.prop] }}
          </template>
        </el-table-column >
      </template>
    </el-table>
  </v-card>
</template>

<script>
export default {
  name: "vd-standart-tab-table",
  props:{
    filterText:String,
    main:Object,
    data:Array
  },
  methods:{
    handlerRowDblClick(row, event){
      this.$emit('dbl-click',row,event)
    }
  }
};
</script>

<style>

</style>


