<template>
  <v-menu :close-on-content-click="false" max-width="1250px" min-width="1210px" max-height="700px" left offset-y v-model="isOpen">
    <v-btn icon slot="activator">
      <v-icon large>face</v-icon>
    </v-btn>
    <template>
      <v-toolbar dense class="standart-tab__toolbar white elevation-1 pa-2">
        <v-text-field class="standart-tab__filter" single-line hide-details label=" ..." v-model="filterText" :append-icon="filterText ? 'clear' : 'search'" :append-icon-cb="() => (filterText='')"></v-text-field>
      </v-toolbar>
      <vd-standart-tab-table ref="refComponent" :filter-text="filterText" :main="toTree" :data="$vdesk.filterTable(filterText,listData)" ></vd-standart-tab-table> 
    </template>    
  </v-menu>
</template>

<script>
import { mapState } from "vuex"
export default {
  name: 'nav-addressbook-button',
  computed: {
    ...mapState({
      listData: state => state.settings.addressbook
    })
  },
  data(){
    return {
      isOpen:false,
      toTree:{
        defSort:{prop: 'fullname', order: 'ascending'},
        key:'fullname',
        columns:[
          {prop:'fullname', label:''},
          {prop:'extphone', label:'.  ',width:100},
          {prop:'outphone', label:'.  ',width:120},
          {prop:'mail', label:'E-Mail'},
          {prop:'department', label:''},
          {prop:'jobtitle', label:''}
        ]
      },
      filterText:'',
    }
  },
  methods: {

  }
}
</script>

<style>

</style>



