<template>
  <v-toolbar dense dark :class="['elevation-0',theme]" :style="{background:color}">
    <el-tooltip v-show="minimize" class="item" effect="dark" :content="title" placement="top-start">
      <v-btn flat @click="$emit('btn-minimize')">
        <v-icon>mdi-window-restore</v-icon>
      </v-btn>
    </el-tooltip>
    <v-toolbar-items>      
      <v-btn flat @click="$emit('btn-click')">
        <div>
          <v-icon>save</v-icon>
          <v-icon>arrow_forward</v-icon>
          <v-icon>close</v-icon>
        </div>
          
      </v-btn>
    </v-toolbar-items>
    <v-spacer></v-spacer>
    <v-btn icon dark @click="$emit('btn-minimize')">
      <v-icon>mdi-window-minimize</v-icon>
    </v-btn>
    <v-btn icon dark @click="$emit('btn-maximize')">
      <v-icon v-if="maximize">mdi-window-restore</v-icon>
      <v-icon v-else>mdi-window-maximize</v-icon>
    </v-btn>
    <v-btn icon dark @click="dialogVisible = false">
      <v-icon>mdi-window-close</v-icon>
    </v-btn>
  </v-toolbar>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "vd-standart-tab-dialog-toolbar",
  props:{
    value:Boolean,
    title:String,
    maximize:{
      type:Boolean,
      default:false
    },
    minimize:{
      type:Boolean,
      default:false
    }
  },
  computed: {
    dialogVisible:{
      get(){
        return this.value;
      },
      set(val){
        this.$emit('input', val)
      }
    },
    ...mapState({
      theme: state => state.worker_data.workerData.theme,
      color: state => state.worker_data.workerData.color
    })      
  }
};
</script>

<style>

</style>


