<template>
  <v-toolbar dense class="standart-tab__toolbar white elevation-1 mb-2">
    <v-btn flat icon @click="$emit('btn-click','add')">
      <v-icon >add_box</v-icon>        
    </v-btn>
    <v-btn flat icon @click="$emit('btn-click','edit')">
      <v-icon>edit</v-icon>
    </v-btn>
    <v-btn flat icon @click="$emit('btn-click','delete')">
      <v-icon>delete</v-icon>
    </v-btn>
    <v-btn v-if="printBtn" flat icon @click="$emit('btn-click','print')">
      <v-icon>print</v-icon>
    </v-btn>
    <v-spacer></v-spacer>
    <v-text-field class="standart-tab__filter" single-line hide-details label=" ..." v-model="filterText" :append-icon="filterText ? 'clear' : 'search'" :append-icon-cb="() => (filterText='')"></v-text-field>
  </v-toolbar>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "vd-standart-tab-toolbar",
  props:{
    value:String,
    printBtn:{
      type:Boolean,
      default:false
    }
  },
  computed: {
    filterText:{
      get(){
        return this.value;
      },
      set(val){
        this.$emit('input', val)
      }
    }
  }
};
</script>

<style>

</style>


