<template>
  <div class="standart-tab filling-links__main pa-2" v-loading="loading">
    <vd-standart-tab-toolbar @btn-click="handlerBtnClick" v-model="filterText"></vd-standart-tab-toolbar>

    <v-card class="standart-tab__tree">
      <el-table @row-dblclick="handlerEditClick" :default-sort = "{prop: 'id', order: 'ascending'}" current-row-key="id" size="mini" ref="refComponent" highlight-current-row :data="$vdesk.filterTable(filterText,listData)">
        <el-table-column sortable prop="id" label="" width="80"></el-table-column >
        <el-table-column sortable prop="name" label="" width="auto" show-overflow-tooltip></el-table-column >
        <el-table-column sortable prop="link" label="" width="auto" show-overflow-tooltip></el-table-column >
        <el-table-column sortable prop="screen" label="" width="auto" show-overflow-tooltip></el-table-column >
      </el-table>
    </v-card>   

    <v-dialog v-model="dialogVisible" max-width="500px" :overlay="true" scrollable>
      <v-card tile>
        <vd-standart-tab-dialog-toolbar v-model="dialogVisible" @btn-click="saveDataAndClose"></vd-standart-tab-dialog-toolbar>
        <v-card-text class="h-100">
          <table class="w-100 align-right" style="border-spacing:8px;">
            <tr>
              <td class="title">:</td>
              <td>
                <el-input :error="selectedDataValidationErrors.name" v-model="selectedData.name"></el-input>
              </td>
            </tr>
            <tr>
              <td class="title">URL:</td>
              <td>
                <el-input v-model="selectedData.link"></el-input>
              </td>
            </tr>
             <tr>
              <td class="title">:</td>
              <td>
                <el-input v-model="selectedData.screen"></el-input>
              </td>
            </tr>
          </table>
        </v-card-text>        
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "filling-links",
  props:{
    componentId:String
  },
  data(){
    return {
      filterText:'',
      dialogVisible:false,
      selectedDataValidationErrors:{
        name:false
      },
      selectedData:{
        id:null,
        name:'',
        orders:null,
        visibility:null,
        icon:null,
        description:null
      },
      type:'new',
      loading:false
    }
  },
  computed: {
    ...mapState({
      listData: state => state.filling.links,
      theme: state => state.worker_data.workerData.theme,
      color: state => state.worker_data.workerData.color
    })
  },
  methods:{
    handlerBtnClick(type){
      switch(type){
        case 'add':
          this.handlerAddClick()
          break;
        case 'edit':
          this.handlerEditClick()
          break;
        case 'delete':
          this.handlerDeleteClick()
          break;
      }
    },
    handlerDeleteClick(){
      let node = this.$refs.refComponent.store.states.currentRow;
      if(node){
        this.$confirm(`    - ${node.name}. ?`, 'Warning', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.selectedData = {id:node.id};
          this.type="delete"
          this.$vdesk.sendUpdate(this)
          this.$message({type: 'success',message: ' '});
        }).catch(() => {
          this.$message({type: 'info',message: ' '});
        });
      }      
    },
    handlerEditClick(row=this.$refs.refComponent.store.states.currentRow){
      if(row){
        this.selectedData = Object.assign({},row)
        this.type="update"
        this.dialogVisible=true
      } 
    },
    handlerAddClick() {
      this.selectedData = {id:null,name:''}
      this.type="new"
      this.dialogVisible = true
    },
    saveDataAndClose() {
      let isValid = true
      Object.keys(this.selectedDataValidationErrors).forEach( el => {
        if(!this.selectedData[el]){
          isValid = false
          this.selectedDataValidationErrors[el]=true;
          setTimeout(()=>{this.selectedDataValidationErrors[el]=false},2000)
        }
      })
      if(isValid){
        this.$vdesk.sendUpdate(this)
      }
    }
  }
};
</script>

<style>

</style>


