<template>
  <v-system-bar window dark :style="{background:workerData.color}" :class="workerData.theme">
    <v-toolbar-side-icon @click.stop="toggleClick"></v-toolbar-side-icon>
    <v-toolbar-title v-localize="{i: 'vdesk'}"></v-toolbar-title>
    <v-spacer></v-spacer>
    <!--<v-icon>network_wifi</v-icon>
    <v-icon>signal_cellular_null</v-icon>
    <v-icon>battery_full</v-icon>-->
    <nav-settings-button @toggle-settings="toggleSettings"></nav-settings-button>
    <v-btn small icon>
      <v-icon class="ma-0">mdi-exit-to-app</v-icon>
    </v-btn>
    <v-tooltip bottom>
      <v-icon slot="activator" @click.stop="handlerLogout">mdi-exit-to-app</v-icon>
      <span></span>
    </v-tooltip>
    <nav-worker-button :title-btn="`${workerData.surname} ${workerData.name} ${workerData.patronymic}`"></nav-worker-button> 
    <span>12:30</span>
    
    
  </v-system-bar>
  <!--<v-toolbar dense dark :style="{background:workerData.color}" :class="workerData.theme">
    <v-toolbar-side-icon @click.stop="toggleClick"></v-toolbar-side-icon>
    <v-toolbar-title v-localize="{i: 'vdesk'}"></v-toolbar-title>
    <v-tooltip bottom>
      <v-btn icon flat @click.stop="toggleClick" slot="activator">
        <v-icon >add_circle</v-icon>
      </v-btn>
      <span> </span>
    </v-tooltip>
    <v-spacer></v-spacer>

    <v-spacer></v-spacer>
    <nav-settings-button @toggle-settings="toggleSettings"></nav-settings-button>
    <nav-notice-button></nav-notice-button>
    <v-tooltip bottom>
      <v-btn icon flat @click.stop="handlerLogout" slot="activator">
        <v-icon>mdi-exit-to-app</v-icon>
      </v-btn>
      <span></span>
    </v-tooltip>
    <v-tooltip bottom>
      <v-btn flat icon slot="activator" class="inverted">
        <v-progress-circular :size="35" :width="5" :value="75" class="inverted" >
          <v-avatar :size="25">
            <!- <v-icon dark>account_circle</v-icon>-
            <img src="http://10.72.1.253/images/43846936.jpg" />
          </v-avatar>
        </v-progress-circular>
      </v-btn>
      <span>
        {{ workerData.surname }} {{ workerData.name }} {{ workerData.patronymic }}<br>
        : 231/234<br>
        : 30
      </span>
    </v-tooltip>--
    
     <nav-worker-button :title-btn="`${workerData.surname} ${workerData.name} ${workerData.patronymic}`"></nav-worker-button> 
  </v-toolbar>-->
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'vd-navbar',
  computed: mapState({
    workerData: state => state.worker_data.workerData,
  }),
  methods: {
    toggleClick() {
      if (this.$store.state.settings.sideIsCollapsed) {
        this.$store.dispatch('sideExpand');
      } else {
        this.$store.dispatch('sideCollapse');
      }
    },
    toggleSettings(){
       this.$emit('toggle-settings')
    }
  }
}
</script>

<style>

</style>



