<template>
  <div class="standart-tab directorys-accessgroups__main pa-2" v-loading="loading">
    <vd-standart-tab-toolbar @btn-click="handlerBtnClick" v-model="filterText"></vd-standart-tab-toolbar>
    <vd-standart-tab-table ref="refComponent" @dbl-click="handlerEditClick" :filter-text="filterText" :main="toTree" :data="$vdesk.filterTable(filterText,listData)" ></vd-standart-tab-table>
    <vd-standart-tab-dialog v-model="dialogVisible" @btn-click="saveDataAndClose">
      <el-form size="small" ref="form" :model="selectedData" label-width="75px">
        <el-form-item label="">
          <el-input :disabled="selectedData.const" :error="selectedDataValidationErrors.name" placeholder=" " v-model="selectedData.name"></el-input>
        </el-form-item>
      </el-form>
    </vd-standart-tab-dialog>

  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "directorys-accessgroups",
  props:{
    componentId:String
  },
  data(){
    return {
      toTree:{
        defSort:{prop: 'id', order: 'ascending'},
        key:'id',
        columns:[
          {prop:'id', label:'',width:80},
          {prop:'name', label:''}
        ]
      },
      filterText:'',
      dialogVisible:false,
      selectedDataValidationErrors:{
        name:false
      },
      selectedData:{
        id:null,
        name:''
      },
      type:'new',
      loading:false
    }
  },
  computed: {
    ...mapState({
      listData: state => state.directorys.accessgroups,
      theme: state => state.worker_data.workerData.theme,
      color: state => state.worker_data.workerData.color
    })
  },
  methods:{
    handlerBtnClick(type){
      switch(type){
        case 'add':
          this.handlerAddClick()
          break;
        case 'edit':
          this.handlerEditClick()
          break;
        case 'delete':
          this.handlerDeleteClick()
          break;
      }
    },
    handlerDeleteClick(){
      let node = this.$refs.refComponent.$children[0].$children[0].store.states.currentRow;
      if(node){
        if(node.const){
          this.$message({type: 'error',message: '   '});
        } else {
          this.$confirm(`    - ${node.name}. ?`, 'Warning', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.selectedData = {
              id:node.id,
							icon:node.icon,
							groups:node.groups,
              name:node.name,
              _parentId:node._parentId,
              order:node.order,
              link:node.link,
              noDelete:!!node.noDelete,
              type:node.type
            };
            this.type="delete"
            this.$vdesk.sendUpdate(this)
            this.$message({type: 'success',message: ' '});
          }).catch(() => {
            this.$message({type: 'info',message: ' '});
          });
        }
      }
    },
    handlerEditClick(row=this.$refs.refComponent.$children[0].$children[0].store.states.currentRow){
      if(row){
        this.selectedData = Object.assign({},row)
        this.type="update"
        this.dialogVisible=true
      } 
    },
    handlerAddClick() {
      this.selectedData = {id:null,name:''}
      this.type="new"
      this.dialogVisible = true
    },
    saveDataAndClose() {
      let isValid = true
      Object.keys(this.selectedDataValidationErrors).forEach( el => {
        if(!this.selectedData[el]){
          isValid = false
          this.selectedDataValidationErrors[el]=true;
          setTimeout(()=>{this.selectedDataValidationErrors[el]=false},2000)
        }
      })
      if(isValid){
        this.$vdesk.sendUpdate(this)
      }
    }
  }
};
</script>

<style>

</style>


