<template>
  <div v-if="tiketData && (view!=='none'|| inTab)" :class="['tikets-main-show pb-2 px-2',{'show--in-tab':inTab}]">
    <v-alert warning v-model="confirmed" class="tikets-show__header">
      <div class="title">
        <b v-html="confirmedData.text"></b>
      </div>
      <v-btn small color="success" @click="handlerConfirm(confirmedData.type,true)">{{ $locale({i: 'iagree'}) }}</v-btn>
      <v-btn small color="error" @click="handlerConfirm(confirmedData.type,false)">{{ $locale({i: 'disagree'}) }}</v-btn>
    </v-alert>
    <v-toolbar dense class="tikets-show__toolbar white elevation-1">
      <!--<v-toolbar-title class="ml-1">
        <v-chip class="blue lighten-1 white--text"><b>{{ tiketData.id }}</b></v-chip>
      </v-toolbar-title>-->
      <template v-for="btn in toolbar">
        <v-tooltip bottom v-if="view==='left' && !inTab" :key="btn.id">
          <v-btn small light icon flat :color="btn.color" :disabled="btn.disabled" @click="btn.action" slot="activator">
            <v-icon>{{btn.icon}}</v-icon>
          </v-btn>
          <span>{{ btn.label }}</span>
        </v-tooltip>
        <v-btn light flat v-else :key="btn.id" :color="btn.color" :disabled="btn.disabled" @click="btn.action">
          <v-icon left>{{btn.icon}}</v-icon>
          {{ btn.label }}
        </v-btn>
      </template>
      <v-spacer></v-spacer>
      <v-menu :disabled="isDisabled" offset-y left :nudge-top="30">
        <v-tooltip top slot="activator">
          <v-btn icon small class="" :disabled="isDisabled" slot="activator">
            <v-icon class="blue--text">mdi-apps</v-icon>
          </v-btn>
          <span></span>
        </v-tooltip>
        <v-list dense>
          <v-list-tile @click="handlerActionBtn('file')">
            <v-list-tile-title> </v-list-tile-title>
          </v-list-tile>
          <v-list-tile @click="handlerActionBtn('telnet')">
            <v-list-tile-title>Telnet</v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-menu>
    </v-toolbar>
    <v-card class="tikets-show__info-panel pa-2">
      <v-btn block :disabled="isDisabled" flat small :color="importanceColor" @click="handlerImportance" :class="['ma-0',{'ani-flash':tiketData.importance}]">
        <template v-if="tiketData.importance">
          <v-icon class="">mdi-alert-decagram</v-icon>
          <span class="mx-2">{{ $locale({i: 'IMPORTANCE.HIGH'}) }}</span>
          <v-icon>mdi-alert-decagram</v-icon>
        </template>
        <span v-else>{{ $locale({i: 'IMPORTANCE.NORMAL'}) }}</span>
      </v-btn>
      <vd-tikets-view-infocard-item icon="mdi-account" :title="$locale({i: 'sender'})" :text="`${ tiketData.sendersurname } ${ tiketData.sendername } ${ tiketData.senderpatronymic }`" :copy-data="senderFullname"></vd-tikets-view-infocard-item>
      <vd-tikets-view-infocard-item icon="mdi-city" :title="$locale({i: 'department'})" :text="getDepartment(tiketData.department)" :copy-data="senderFullname"></vd-tikets-view-infocard-item>
      <vd-tikets-view-infocard-item icon="mdi-phone-classic" :title="$locale({i: 'contactphone'})" :text="tiketData.senderphone" :copy-data="tiketData.senderphone"></vd-tikets-view-infocard-item>
      <vd-tikets-view-infocard-item icon="mdi-email-outline" :title="$locale({i: 'email'})" :text="tiketData.sendermail">
        <v-btn slot="right" small flat icon color="blue" class="ma-0" @click="commentPanelShow = !commentPanelShow"><!--  v-clipboard="tiketData.sendermail" -->
          <v-icon>mdi-send</v-icon>
        </v-btn>
      </vd-tikets-view-infocard-item>
      <vd-tikets-view-infocard-item icon="mdi-desktop-classic" :title="$locale({i: 'computerName'})" :copy-data="tiketData.sendercompname">
        <v-btn flat small :disabled="isDisabled" class="tikets-show__info-panel__btn orange--text btn-justify-left caption ma-0" @click="handleCommandAction(tiketData.sendercompname, 'control')">{{tiketData.sendercompname }}</v-btn>
      </vd-tikets-view-infocard-item>
      <vd-tikets-view-infocard-item icon="mdi-map-marker-outline" :title="$locale({i: 'ipAddress'})" :copy-data="tiketData.senderip">
        <v-btn flat small :disabled="isDisabled" class="tikets-show__info-panel__btn orange--text btn-justify-left caption ma-0" @click="handleCommandAction(tiketData.senderip, 'control')">{{ tiketData.senderip }}</v-btn>
      </vd-tikets-view-infocard-item>
      <vd-tikets-view-infocard-item icon="mdi-alarm-plus" :title="$locale({i: 'tikets.dateReceipt'})" :text="modeDate(tiketData.startdate)" :copy-data="modeDate(tiketData.startdate)"></vd-tikets-view-infocard-item>
      <vd-tikets-view-infocard-item v-if="tiketData.enddate" icon="mdi-alarm-check" :title="$locale({i: 'tikets.endDate'})" :text="modeDate(tiketData.enddate)" :copy-data="modeDate(tiketData.enddate)"></vd-tikets-view-infocard-item>
      <vd-tikets-view-infocard-item icon="mdi-alarm-off" :title="$locale({i: 'tikets.deadlineCompletion'})" :copy-data="tiketData.senderphone">
        <template>
          <span v-if="postpone.menu===1" :class="postponeClass">{{ modeDate(tiketData.deadline) }}</span>
          <el-date-picker v-if="postpone.menu===2" style="width:180px" :picker-options="postpone.options" size="mini" popper-class="postpone-datepicker" type="datetime" :format="postpone.format" v-model="postpone.date" :placeholder="$locale({i: 'dateSelect'})"></el-date-picker>
          <el-input v-if="postpone.menu===3" :class="{'postpone-error':postpone.error}" style="width:180px" size="mini" :placeholder="$locale({i: 'setReason'})" v-model="postpone.reason"></el-input>
        </template>
        <template slot="right">
          <v-btn v-if="postpone.menu===1" flat icon small color="red" :disabled="isDisabled" class="ma-0" @click.native.stop="handlerPostpone(1)">
            <v-icon>mdi-pencil</v-icon>
          </v-btn>
          <v-btn v-if="postpone.menu===2" flat icon small color="blue" :disabled="isDisabled" class="ma-0" @click.native.stop="handlerPostpone(2)">
            <v-icon>mdi-forward</v-icon>
          </v-btn>
          <v-btn v-if="postpone.menu===3" flat icon small color="green" :disabled="isDisabled" class="ma-0" @click.native.stop="handlerPostpone(3)">
            <v-icon>mdi-check</v-icon>
          </v-btn>
        </template>        
      </vd-tikets-view-infocard-item>
      <vd-tikets-view-infocard-item v-if="tiketData.reactiontime" icon="mdi-timer" :title="$locale({i: 'tikets.reactionTime'})" :text="humanize(tiketData.reactiontime)" :copy-data="humanize(tiketData.reactiontime)"></vd-tikets-view-infocard-item>
      <vd-tikets-view-infocard-item v-if="tiketData.laborexpenditures" icon="mdi-timer" title="" :text="`${tiketData.laborexpenditures} (/)`" :copy-data="tiketData.laborexpenditures"></vd-tikets-view-infocard-item>
      <vd-tikets-view-infocard-item icon="mdi-account-outline" :title="$locale({i: 'tikets.performer'})" :copy-data="tiketData.senderphone">
        <template>
          <el-select v-if="worker.visible" v-model="worker.changed" size="mini">
            <el-option v-for="item in workersList" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
          <span v-else>{{ tiketData.workerfio }}</span>
        </template>
        <template slot="right">
          <v-btn v-if="worker.visible" flat icon small color="green" :disabled="isDisabled" class="ma-0" @click.native.stop="handlerChangeWorker">
            <v-icon>mdi-check</v-icon>
          </v-btn>
          <v-btn v-else flat icon small color="red" :disabled="isDisabled" class="ma-0" @click.native.stop="handlerChangeWorker">
            <v-icon>mdi-pencil</v-icon>
          </v-btn>
        </template>
      </vd-tikets-view-infocard-item>
      <vd-tikets-view-infocard-item v-if="tiketData.classifiername" icon="mdi-book-variant" title="" :text="tiketData.classifiername" :copy-data="tiketData.classifiername"></vd-tikets-view-infocard-item>
    </v-card>
    <v-card class="tikets-show__description-panel">
      <v-system-bar window class="tikets-show__description-panel__attachment grey lighten-4" v-if="tiketData.attachment">
        <!--<v-btn v-for="(item,idx) in tiketData.attachment" :key="item" small color="info" flat class="ma-0 lowercase" @click="attachClick(tiketData.id,item)">
          <v-icon left class="mr-1">description</v-icon>
          <span class="first-char-upper">{{ attachmentName(item,idx+1) }}</span>
        </v-btn>-->
        <v-btn small color="info" flat class="ma-0 lowercase" @click="attachClick(tiketData.id,tiketData.attachment)">
          <v-icon left class="mr-1">description</v-icon>
          <span class="first-char-upper">{{ attachmentName(tiketData.attachment,1) }}</span>
        </v-btn>
      </v-system-bar>
      <div class="pa-3 subheading">
        {{tiketData.description}}
      </div>
    </v-card>
    <v-card class="tikets-show__solution-panel">
      <v-text-field full-width hide-details multi-line v-model="solution" :disabled="isDisabled" :placeholder="$locale({i: 'enterProblemSolution'})"></v-text-field>
    </v-card>      
    <v-card class="tikets-show__comment-panel">
      <v-system-bar window class="tikets-show__comment-panel-tools">
        <v-btn flat icon class="ma-0" :disabled="isDisabled" @click="sendCommentMail">
          <v-icon class="mx-0">mdi-email</v-icon>
        </v-btn>
        <v-btn flat icon class="ma-0" :disabled="isDisabled" @click="sendCommentAttachmentClick">
          <v-icon class="mx-0">mdi-paperclip</v-icon>
          <input class="d-none" @change="changeAttachmentFiles" ref="tikets-my-send-comment-attachment-input" type="file" multiple>
        </v-btn>
        <v-spacer></v-spacer>
        <span>{{ $locale({i: 'ReviewForSender'}) }}</span>
      </v-system-bar>
      <section class="tikets-show__comment-panel__text">
        <v-text-field full-width hide-details textarea v-model="comment.text" :disabled="isDisabled" :placeholder="$locale({i: 'enterTextMessage'})"></v-text-field>
        <div v-if="comment.attachment.filesList.length>0" class="tikets-show__comment-panel__attachment">
          <el-card>
            <div slot="header" class="clearfix">
              <span style="line-height: 36px;"> - {{ comment.attachment.count }}  {{ comment.attachment.summText }}</span>
            </div>
            <el-upload multiple action="#" :auto-upload="false" :on-remove="removeAttachmentFiles" :file-list="comment.attachment.filesList"></el-upload>
          </el-card>
        </div>
      </section>
    </v-card>
    <v-dialog v-model="commentPanelShow" max-width="900">
      <v-card>
      <v-toolbar card dense dark :class="['elevation-0',theme]" :style="{background:color}">
        <v-toolbar-title v-localize="{i: 'ReviewForSender'}"></v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click.native="commentPanelShow = false">
          <v-icon>close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-system-bar window class="tikets-show__comment-panel-tools">
        <v-btn flat class="ma-0" :disabled="isDisabled" @click="sendCommentMail">
          <v-icon left class="mx-0">mdi-email</v-icon>
          
        </v-btn>
        <v-btn flat class="ma-0" :disabled="isDisabled" @click="sendCommentAttachmentClick">
          <v-icon left class="mx-0">mdi-paperclip</v-icon>
                    
          <input class="d-none" @change="changeAttachmentFiles" ref="tikets-my-send-comment-attachment-input" type="file" multiple>
        </v-btn>
      </v-system-bar>
      <!--<v-card class="tikets-show__comment-panel__card">-->
      <section class="tikets-show__comment-panel__text">
        <v-text-field full-width hide-details textarea v-model="comment.text" :disabled="isDisabled" :placeholder="$locale({i: 'enterTextMessage'})"></v-text-field>
        <div v-if="comment.attachment.filesList.length>0" class="tikets-show__comment-panel__attachment">
          <el-card>
            <div slot="header" class="clearfix">
              <span style="line-height: 36px;"> - {{ comment.attachment.count }}  {{ comment.attachment.summText }}</span>
            </div>
            <el-upload multiple action="#" :auto-upload="false" :on-remove="removeAttachmentFiles" :file-list="comment.attachment.filesList"></el-upload>
          </el-card>
        </div>
      </section>
      <!--</v-card>-->
      </v-card>
    </v-dialog>
    <v-card class="tikets-show__chat-panel elevation-1" shadow="never">
      <div class="tikets-show__chat-panel__input">
        <v-text-field class="pa-0" :error="sendChat.error" solo hide-details v-model="sendChat.text" :disabled="isDisabled" :label="$locale({i: 'enterTextMessage'})" append-icon="mdi-send" :append-icon-cb="handlerSendChat"></v-text-field>
      </div>
      <el-tabs v-model="chatSelected" type="border-card" tab-position="bottom" style="border:0px;">
        <el-tab-pane label="" name="All" class="ma-0">
          <template v-for="log in tiketData.logs">
              <el-row :gutter="10" :key="log.date" class="ma-2">
                <!--<el-col :push="tiketData.workerfio!==log.who ? 20 : 0" :span="4">
                  <avatar :username="log.who" :size="30"></avatar>
                </el-col>-->
                <el-col :pull="tiketData.workerfio!==log.who ? 0 : 0" :span="24" class="elevation-2 pa-1">
                  <span class="caption green--text d-inline-flex">
                    <avatar class="float:left" :username="log.who" :size="30"></avatar>
                    <div class="pa-2">{{ log.who }}:</div>
                  </span>
                  <v-divider></v-divider>
                  <div class="caption" v-html="log.text"></div>
                  <span class="caption grey--text text--lighten-1" style="float:right">
                    <v-tooltip bottom>
                      <timeago :since="log.date" slot="activator"></timeago>
                      <span>{{ modeDate(log.date) }}</span>
                    </v-tooltip>                
                  </span>
                </el-col>
              </el-row>
          </template>        
        </el-tab-pane>
        <el-tab-pane label="" name="Logs" class="ma-0">
          <template v-for="log in tiketData.logs">
              <el-row :gutter="10" :key="log.date" class="ma-2">
                <!--<el-col :push="tiketData.workerfio!==log.who ? 20 : 0" :span="4">
                  <avatar :username="log.who" :size="30"></avatar>
                </el-col>-->
                <el-col :pull="tiketData.workerfio!==log.who ? 0 : 0" :span="24" class="elevation-2 pa-1">
                  <span class="caption green--text d-inline-flex">
                    <avatar class="float:left" :username="log.who" :size="30"></avatar>
                    <div class="pa-2">{{ log.who }}:</div>
                  </span>
                  <v-divider></v-divider>
                  <div class="caption" v-html="log.text"></div>
                  <span class="caption grey--text text--lighten-1" style="float:right">
                    <v-tooltip bottom>
                      <timeago :since="log.date" slot="activator"></timeago>
                      <span>{{ modeDate(log.date) }}</span>
                    </v-tooltip>                
                  </span>
                </el-col>
              </el-row>
            </template>
        </el-tab-pane>
        <el-tab-pane label="" name="Chat" class="ma-0">
          <template v-for="log in tiketData.logs">
              <el-row :gutter="10" :key="log.date" class="ma-2">
                <!--<el-col :push="tiketData.workerfio!==log.who ? 20 : 0" :span="4">
                  <avatar :username="log.who" :size="30"></avatar>
                </el-col>-->
                <el-col :pull="tiketData.workerfio!==log.who ? 0 : 0" :span="24" class="elevation-2 pa-1">
                  <span class="caption green--text d-inline-flex">
                    <avatar class="float:left" :username="log.who" :size="30"></avatar>
                    <div class="pa-2">{{ log.who }}:</div>
                  </span>
                  <v-divider></v-divider>
                  <div class="caption" v-html="log.text"></div>
                  <span class="caption grey--text text--lighten-1" style="float:right">
                    <v-tooltip bottom>
                      <timeago :since="log.date" slot="activator"></timeago>
                      <span>{{ modeDate(log.date) }}</span>
                    </v-tooltip>                
                  </span>
                </el-col>
              </el-row>
            </template>
        </el-tab-pane>
      </el-tabs>
    </v-card>

    <vd-standart-tab-dialog v-model="completeDialogShow" @btn-click="handlerBtnStatusClick">
      <el-form ref="form" label-width="250px">
        <el-form-item label="(.)">
          <el-input-number v-model="laborexpenditures" :min="1"></el-input-number>
        </el-form-item>
      </el-form>
    </vd-standart-tab-dialog>

    <vd-standart-tab-dialog v-model="classifierDialogShow" overlay @btn-click="handlerBtnClassifierClick">
      <el-form ref="form" label-width="250px">
        <el-form-item label="">
          <el-cascader :props="classifierProps" separator="." filterable expand-trigger="hover" :options="classifierData" v-model="classifierDataChanged"></el-cascader>
        </el-form-item>
      </el-form>
    </vd-standart-tab-dialog>

    <!--<el-card class="tikets-show__chat-panel elevation-1">
      <div slot="header" class="clearfix">
        <span>/</span>
      </div>
      <div class="tikets-show__chat-panel__input">
        <v-text-field class="pa-0" :error="sendChat.error" solo hide-details v-model="sendChat.text" :disabled="isDisabled" :label="$locale({i: 'enterTextMessage'})" append-icon="mdi-send" :append-icon-cb="handlerSendChat"></v-text-field>
      </div>
      <div class="tikets-show__chat-panel__list pa-0 chat">  v-chat-scroll --
        <template v-for="log in tiketData.logs">
          <el-row :gutter="10" :key="log.date" class="ma-2">
            <el-col :push="tiketData.workerfio!==log.who ? 20 : 0" :span="4">
              <avatar :username="log.who" :size="30"></avatar>
            </el-col>
            <el-col :pull="tiketData.workerfio!==log.who ? 4 : 0" :span="20" class="elevation-2 pa-1">
              <span class="caption green--text">{{ log.who }}:</span>
              <v-divider></v-divider>
              <div class="caption" v-html="log.text"></div>
              <span class="caption grey--text text--lighten-1" style="float:right">
                <v-tooltip bottom>
                  <timeago :since="log.date" slot="activator"></timeago>
                  <span>{{ modeDate(log.date) }}</span>
                </v-tooltip>                
              </span>
            </el-col>
          </el-row>
        </template>
      </div>
      
    </el-card>-->
    <iframe class="d-none" ref="protocolFrame"></iframe>
  </div>
</template>

<script>
import { Avatar } from 'vue-avatar'
import moment from 'moment'
import { mapState } from 'vuex'
import { Loading } from 'element-ui';
export default {
  name: 'tikets-view',
  components: {
    Avatar
  },
  props: {
    inTab: {
      type: Boolean,
      default: false
    },
    type: {
      type: String
    },
    id: {
      type: String
    },
    value:Object
  },
  data() {
    return {
      classifierProps:{
        label:"name",
        value:"id"
      },
      laborexpenditures:null,
      classifierDataChanged:[],
      compleetStatus:"complete",
      completeDialogShow:false,
      classifierDialogShow:false,
      commentPanelShow:false,
      chatSelected:'All',
      sendChat: {
        text: '',
        error: false
      },
      end: ['', '', '', ''],
      comment: {
        text: '',
        attachment: {
          summ: 0,
          count: 0,
          subText: '',
          filesList: []
        },
        formData: new FormData()
      },
      confirmedData:{
        text:'',
        type:''
      },
      worker: {
        visible:false,
        changed:''
      },
      importance: false,
      postpone: {
        menu:1,
        error:false,
        format:'dd.MM.yyyy HH:mm:ss',
        date:'',
        reason: '',
        options:{
          firstDayOfWeek:1
        }
      }
    }
  },
  computed: {
    ...mapState({
      classifierData: state => state.directorys.classifier,
      departmentsData: state => state.directorys.departments,
      view: state => state.worker_data.workerData.tiketsview,
      isAdmin: state => state.worker_data.isAdmin,
      workerData: state => state.worker_data.workerData,
      workersList: state => state.settings.lists.workers,
      theme: state => state.worker_data.workerData.theme,
      color: state => state.worker_data.workerData.color
    }),
    postponeClass(){
      return (this.tiketData.statusconfirm===5 && this.tiketData.postpone) ? 'deep-orange--text' : ''//
    },
    importanceColor(){
      return this.tiketData.importance ? 'red' : 'teal'
    },
    senderFullname(){
      return `${this.tiketData.sendersurname} ${this.tiketData.sendername} ${this.tiketData.senderpatronymic}`
    },
    tiketID() {
      return this.id
    },
    confirmed() {
      if (this.isAdmin && !this.isMy && this.tiketData.statusconfirm) {
        switch (this.tiketData.statusconfirm) {
          case 2:
            this.confirmedData.text = `    .<br> : ${this.tiketData.solution}`;
            this.confirmedData.type = 'refuse';
            return true;
          case 5:
            this.confirmedData.text = `      ${this.modeDate(this.tiketData.postpone)}.<br> : ${this.tiketData.postponereason}`;
            this.confirmedData.type = 'postpone';
            return true;
        }        
      } else {
        return false
      }
    },
    tiketData() {
      let data = this.value || this.$store.getters.getTiketsShow(this.type, this.tiketID);
      if(data){
        this.postpone.date = new Date(data.postpone || data.deadline)
        this.worker.changed = data.worker
        this.classifierDialogShow = !data.classifiername ? true : false
      }      
      return data;
    },
    solution: {
      get() {
        return this.tiketData.solution
      },
      set(value) {
        this.$store.dispatch('editTiketData', { field: 'solution', value: value, type: this.type, id: this.tiketData.id });
      }
    },
    toolbar() {
      let self=this;
      return [
        {
          id: 'tiket-complete',
          label: '',
          disabled: this.isCompleted || (!this.isMy && !this.isAdmin),
          color: 'green',
          icon: 'mdi-check-circle-outline',
          action() {
            self.completeDialogShow = true
          }
        },
        {
          id: 'tiket-refuse',
          label: '',
          disabled: this.isCompleted || (!this.isMy && !this.isAdmin),
          color: 'red',
          icon: 'mdi-close-circle-outline',
          action() {
            const h = self.$createElement;
            if (self.solution) {
              self.compleetStatus = 'refuse'
              self.completeDialogShow = true
            } else {
              self.$msgbox({
                type: 'error',
                title: '!',
                message: h('p', null, [
                  h('span', null, '  '),
                  h('b', null, ''),
                  h('span', null, '!')
                ]),
                confirmButtonText: 'OK'//' !', '',
              });
            }
          }
        },
        {
          id: 'tiket-undo',
          label: '  ',
          disabled: !this.isCompleted || !this.isAdmin,
          color: 'cyan',
          icon: 'mdi-reply',
          action() {
            self.sendEdit({status: 'return'})
          }
        },
        {
          id: 'tiket-print',
          label: '',
          disabled: false,
          color: 'black',
          icon: 'mdi-printer',
          action() {
            self.$store.dispatch('printTiket', Object.assign({ workerFioLong: `${self.workerData.surname} ${self.workerData.name} ${self.workerData.patronymic}` }, self.tiketData));
          }
        }
      ]
    },  
    isCompleted() {
      return this.tiketData.status < 3;
    },
    isMy() {
      if (!!this.tiketData) {
        return this.workerData.login === this.tiketData.worker;
      }
    },
    isDisabled() {
      return this.isCompleted || (!this.isMy && !this.isAdmin)
    },
    workerFullname() {
      return `${this.workerData.surname} ${this.workerData.name.charAt(0)}.${this.workerData.patronymic.charAt(0)}.`
    }
  },
  methods: {
    humanize(second){
      return moment.duration(second, "second").humanize()
    },
    getDepartment(sid){
      let department = this.departmentsData.find( el => el.sid === sid*1 )
      if(!department){
        return ""
      }
      return department.name
    },
    handlerBtnStatusClick(){
      this.sendEdit({status: this.compleetStatus, solution: this.solution, isMine: this.tiketData.isMine, laborexpenditures: this.laborexpenditures})
      this.completeDialogShow = false
      this.closeTiket();
    },
    handlerBtnClassifierClick(){
      this.sendEdit({classifier: this.classifierDataChanged.pop()})
      this.classifierDataChanged = []
    },
    handlerPostpone(stage){
      switch(stage){
        case 1:
          this.postpone.menu=2
        break;
        case 2:
          if(moment(this.postpone.date).diff(moment(this.tiketData.deadline)) > 0){
            this.postpone.menu=3
          } else {
            this.postpone.date=new Date(this.tiketData.postpone || this.tiketData.deadline)
            this.postpone.menu=1
          }          
        break;
        case 3:
          if(!this.isAdmin && !this.postpone.reason){
            this.postpone.error=true
            setTimeout(()=>{this.postpone.error=false},2000)
          } else {
            let data={
              postpone:this.postpone.date,
              reason:this.postpone.reason
            }
            this.sendEdit(data);
            this.postpone.menu=1
          }          
        break;
      }
    },
    handlerChangeWorker(){
      if(this.worker.visible && this.worker.changed!==this.tiketData.worker){
        let data = {
          newWorkerLogin: this.worker.changed,
          newWorkerName: this.workersList.find((el) => { return el.id === this.worker.changed; }).name,
          oldWorkerName: this.tiketData.workerfio
        }
        this.sendEdit(data);
        this.closeTiket()
      }
      this.worker.visible=!this.worker.visible
    },
    handlerImportance() {
      this.importance = !this.tiketData.importance;
      this.sendEdit({importance:this.importance});
    },
    handlerConfirm(type, check) {
      let sendStatus = (status) => {
        this.$socket.emit('edittiketstatus', Object.assign({}, this.tiketData, { status: status }));
      }
      switch (type) {
        case 'postpone':
          this.sendEdit({confirm: check,postpone: this.tiketData.postpone})
          break;
        case 'refuse':
          if (check) {
            this.sendEdit({status: 'confirmrefuse'})
          } else {
            this.sendEdit({status: 'noconfirmrefuse'})
          }
          break;
      }
    },    
    handlerActionBtn(command) {
      let link = this.tiketData.sendercompname === '' ? this.tiketData.senderip : this.tiketData.sendercompname;
      this.handleCommandAction(link, command);
    },
    handleCommandAction(link, command, event) {
      switch (command) {
        case 'control':
          this.$refs.protocolFrame.src = `radmin://${link}`
          break;
        default:
          this.$refs.protocolFrame.src = `radmin://${link}/${command}`
          break;
      }
    },
    handlerSendChat() {
      if (this.sendChat.text) {
        this.$socket.emit('send_tiket_chat', { text: this.sendChat.text, tiket: this.tiketData.id, date: moment(), worker: this.tiketData.worker });
      } else {
        this.sendChat.error = true;
        setTimeout(() => { this.sendChat.error = false; }, 2000)
      }
    },
    attachmentName(attach, idx) {
      let ext = attach.split('.')[3];
      return `_${idx}.${ext}`;
    },
    closeTiket() {
      if(this.inTab){
        this.$store.dispatch('deleteMainTab', `tiket${this.tiketData.id}`);  
      } else {
        this.tiketID = null
        this.laborexpenditures = null
        this.classifierDataChanged = []
      }
    },
    sendEdit(data) {
      let send = Object.assign({tiket:this.tiketData.id, worker: this.tiketData.worker }, data);
      this.$socket.emit('tiket_edit', send);
    },
    modeDate(data) {
      return this.$store.getters.momentData(data)
    },
    attachClick(name, attachment) {
      window.open(`/getFile?type=tiketsAttachment&file=${attachment}&newname=${name}`, 'attachment', 'menubar =no,toolbar=no,location =no,status =no')
    },
    setSize(size, bit = 0) {
      if (size > 1024) {
        size = ((size / 1024).toFixed(2)) * 1;
        bit++;
        return this.setSize(size, bit);
      } else {
        return `${size}${this.end[bit]}`;
      }
    },
    addLog(log) {
      this.$store.dispatch('addTiketLog', { id: this.tiketData.id, log: log, type: this.type })
    },
    sendCommentMail() {
      if (this.comment.text) {
        let loadingInstance = Loading.service();
        this.comment.formData.append('id', this.tiketData.id)
        this.comment.formData.append('topic', this.tiketData.topicname)
        this.comment.formData.append('sendermail', this.tiketData.sendermail)
        this.comment.formData.append('worker', JSON.stringify(this.workerData))
        this.comment.formData.append('description', this.tiketData.description)
        this.comment.formData.append('datestart', moment(this.tiketData.startdate).format('DD.MM.YYYY HH:mm:ss'))
        this.comment.formData.append('mail_msg', this.comment.text)
        this.$http.post('/tiketSendMail', this.comment.formData).then(({ data }) => {
          loadingInstance.close();
          data.forEach(el => {
            el.who = el.worker;
            this.addLog(el)
          })
          this.$message.success(' !');
          this.comment.text = ''
          this.comment.attachment = {
            summ: 0,
            count: 0,
            subText: '',
            filesList: []
          }
          this.comment.formData = new FormData()
        });
      }
      else {
        this.$message.error('   !');
      }
    },
    sendCommentAttachmentClick() {
      this.$refs['tikets-my-send-comment-attachment-input'].click();
    },
    changeAttachmentFiles(e) {
      let files = e.target.files;
      if (files.length > 0) {
        for (let i = 0; i < files.length; i++) {
          let sizeText = this.setSize(files[i].size);
          let fileName = `(${sizeText})${files[i].name}`;
          let isValue = true;
          this.comment.attachment.filesList.forEach(function(f, i) {
            if (f.name === fileName) {
              isValue = false;
              return false;
            }
          }, this);
          if (isValue) {
            this.comment.attachment.summ += files[i].size;
            this.comment.attachment.filesList.push({ name: fileName, size: files[i].size, sizeText: sizeText });
          }
          this.comment.formData.append(`mail_file[]`, files[i], files[i].name)
        }
        this.comment.attachment.count = this.comment.attachment.filesList.length;
        this.comment.attachment.summText = this.setSize(this.comment.attachment.summ);
      }
    },
    removeAttachmentFiles(file, fileList) {
      this.comment.attachment.filesList.forEach(function(f, i) {
        if (f.name === file.name) {
          this.comment.attachment.filesList.splice(i, 1);
          this.comment.attachment.count = this.comment.attachment.filesList.length;
          this.comment.attachment.summ -= file.size;
          this.comment.attachment.summText = this.setSize(this.comment.attachment.summ);
        }
      }, this)
    }
  },
  watch: {
    tiketID(val) {
      this.sendChat.text = ''
      this.comment = {
        text: '',
        attachment: {
          summ: 0,
          count: 0,
          subText: '',
          filesList: []
        },
        formData: new FormData()
      }
    }
  }
}
</script>

<style>
.tikets-main-show {
  grid-area: cont !important;
  grid-template-rows: min-content 48px auto auto 1fr minmax(50px, auto) !important;/*minmax(120px, 1fr) minmax(50px, 1fr) minmax(250px, 1fr);*/
  grid-template-columns: 1fr 400px !important;
  grid-gap: 0.5rem !important;
  grid-template-areas:
    "tikets-show__header tikets-show__header" /*min-content*/
    "tikets-show__toolbar tikets-show__info-panel" /*48px*/
    "tikets-show__description-panel tikets-show__info-panel" /*auto*/
    "tikets-show__solution-panel tikets-show__info-panel" /*minmax(50px, max-content)*/
    "tikets-show__solution-panel tikets-show__chat-panel" /*1fr*/
    "tikets-show__comment-panel tikets-show__chat-panel" /*minmax(50px, 250px)*/ !important;
  display: grid !important;
  width: auto !important;
  max-height: 100% !important;
  overflow: hidden !important;
}
.tikets-main-show.show--in-tab{
  grid-template-columns: 1fr 450px !important;
}
.tikets-main-show.show--in-tab .vd-tikets-view-infocard-item{
  font-size: 16px!important;
}
@media screen and (max-height:1045px) {
  .tabs-tikets-main, .tikets-main-show {
    overflow: auto !important;
  }
}
@media screen and (max-width:600px) {
  /*.tikets-main-show {
    background: blue;
  }*/
}
@media only screen and (min-width:600px) and (max-width:960px) {
  /*.tikets-main-show {
    background: yellow;
  }*/
  .tabs-tikets-main {
    overflow: auto !important;
  }
  .tikets-main-show {
    grid-area: cont !important;
    grid-template-rows: auto auto auto auto 250px 240px !important;
    grid-template-columns: 2fr 1fr !important;
    grid-gap: 0.5rem !important;
    grid-template-areas:
      "tikets-show__header tikets-show__header"
      "tikets-show__toolbar tikets-show__toolbar"
      "tikets-show__info-panel tikets-show__info-panel"
      "tikets-show-down-tabs tikets-show-down-tabs"
      "tikets-show__chat-panel tikets-show__chat-panel" !important;
    display: grid !important;
    width: auto !important;
    overflow: visible !important;
  }
}
@media only screen and (min-width:960px) and (max-width:1163px) {
  .tikets-main-show {
    grid-template-rows: min-content 1fr auto auto 150px 200px !important;
    grid-template-columns: 2fr 1fr !important;
    grid-template-areas:
      "tikets-show__header tikets-show__toolbar"
      "tikets-show__description-panel tikets-show__description-panel"
      "tikets-show__solution-panel tikets-show__solution-panel"
      "tikets-show__info-panel tikets-show__info-panel"
      "tikets-show__comment-panel tikets-show__comment-panel"
      "tikets-show__chat-panel tikets-show__chat-panel" !important;
    overflow: auto;
  }
  .tikets-show__header{
    vertical-align: middle !important;
  }
}
@media only screen and (min-width:1163px) and (max-width:1265px) {
  .tikets-main-show {
    grid-template-rows: min-content 1fr 150px 200px !important;
    grid-template-areas:
      "tikets-show__header tikets-show__toolbar"
      "tikets-show__description-panel tikets-show__info-panel"
      "tikets-show__solution-panel tikets-show__chat-panel"
      "tikets-show__comment-panel tikets-show__comment-panel" !important;
    overflow: auto !important;
  }
}
@media only screen and (min-width:1265px) and (max-width:1450px) {
  .tikets-main-show {
    grid-template-rows: min-content min-content 1fr 1fr 150px 200px !important;
    grid-template-areas:
      "tikets-show__header tikets-show__info-panel"
      "tikets-show__toolbar tikets-show__info-panel"
      "tikets-show__description-panel tikets-show__info-panel"
      "tikets-show__solution-panel tikets-show__info-panel"
      "tikets-show__comment-panel tikets-show__comment-panel"
      "tikets-show__chat-panel tikets-show__chat-panel" !important;
    overflow: auto !important;
  }
}
@media only screen and (min-width:1450px) and (max-width:1960px) {
  
}
@media only screen and (min-width:1960px) {
  /*.tikets-main-show {
    background: green;
  }*/
}
.tikets-show__header{
  grid-area: tikets-show__header !important;
  width:100% !important;
}
.tikets-show__toolbar{
  grid-area: tikets-show__toolbar !important;
}
.tikets-show__info-panel{
  grid-area: tikets-show__info-panel !important;
  overflow: auto !important;
}
.tikets-show__info-panel__btn {
  width: 100% !important;
  margin: 2px !important;
  height: 20px !important;
}
.tikets-show__info-panel .postpone-error > input{
  border-color:red !important;
}
.tikets-show__info-panel table{
  width:100% !important;
}
.tikets-show__info-panel table td:nth-child(2){
  border-right: 4px solid white !important;
}
.tikets-show__info-panel table td:nth-child(1),.tikets-show__info-panel table td:nth-child(3){
  width:28px !important;
}
.tikets-show__description-panel {
  grid-area: tikets-show__description-panel !important;
}
.tikets-show__description-panel__attachment{
  position:relative !important;
}
.tikets-show__description-panel__attachment .btn__content {
  padding: 0px 5px !important;
}
.tikets-show__solution-panel {
  grid-area: tikets-show__solution-panel !important;
}
.tikets-show__comment-panel {
  grid-area: tikets-show__comment-panel !important;
  display: grid !important;
  grid-template-rows: auto 1fr !important;
  grid-template-areas: "tikets-show__comment-panel-tools" "tikets-show__comment-panel__text" !important;
}
.tikets-show__comment-panel-tools {
  grid-area: tikets-show__comment-panel-tools !important;
  position: relative !important;
  height: 36px !important;
}
.tikets-show__comment-panel__text {
  grid-area: tikets-show__comment-panel__text !important;
  display: grid !important;
  grid-template-columns: 2fr 1fr !important;
  grid-template-areas: "comment attachment" !important;
  height:250px !important;
  max-height: 100% !important;
  max-width: 100% !important;
  overflow: hidden !important;
}
.tikets-show__comment-panel__attachment {
  grid-area: attachment !important;
  overflow: hidden !important;
}
.tikets-show__comment-panel__attachment>.el-card {
  height: 100% !important;
  display: grid !important;
  grid-template-rows: auto 1fr !important;
}
.tikets-show__comment-panel__attachment .el-card__header {
  padding: 3px 20px !important;
}
.tikets-show__comment-panel__attachment .el-card__header>.clearfix>span {
  line-height: 20px !important;
}
.tikets-show__comment-panel__attachment .el-card__body {
  padding: 0px !important;
  min-width: 200px !important;
  overflow: auto !important;
}
.tikets-show__comment-panel__attachment .el-upload--text {
  display: none !important;
}
.tikets-show__comment-panel__attachment .el-upload-list__item {
  margin: 0px !important;
}
.tikets-show__solution-panel>.input-group, .tikets-show__comment-panel__text .input-group{
  height: 100% !important;
  padding:0px !important;
}
.tikets-show__solution-panel .input-group__details, .tikets-show__comment-panel__text .input-group__details{
  display: none !important;
}
.input-group textarea::-webkit-input-placeholder{color:#b5b5b5;}
.input-group textarea::-moz-placeholder{color:#b5b5b5;}
.input-group textarea:-moz-placeholder{color:#b5b5b5;}
.input-group textarea:-ms-input-placeholder{color:#b5b5b5;}
.tikets-show__solution-panel textarea, .tikets-show__comment-panel__text textarea{
  resize: none !important;
  padding:16px !important;
}
.tikets-show__chat-panel{
  grid-area: tikets-show__chat-panel !important;
  display: grid !important;
  grid-template-rows: auto 1fr !important;
  overflow: hidden !important;
  height: 100% !important;
}
.tikets-show__chat-panel .el-tabs {
  display: grid !important;
  grid-template-rows: 1fr auto !important;
  overflow: hidden !important;
}
.tikets-show__chat-panel .el-tabs__content {
  overflow: auto !important;
  margin:0px !important;
  padding:0px !important;
}
.tikets-show__chat-panel__input {
  width: 100% !important;
}
.btn-justify-left .btn__content {
  justify-content: left !important;
  padding-left: 0px !important;
}
</style>


