<template>
  <v-tooltip bottom>
    <v-btn icon flat @click.stop.prevent="toggleSettings" slot="activator">
      <v-icon>mdi-settings</v-icon>
    </v-btn>
    <span></span>
  </v-tooltip>
</template>

<script>
export default {
  name: 'nav-settings-button',
  computed: {
    settingsShow() {
      return this.$store.state.settings.settingsShow;
    }    
  },
  methods: {
    toggleSettings(){
       this.$emit('toggle-settings')
    }
  }
}
</script>

<style>

</style>



