<template>
  <div class="directorys-users__main pa-2" v-loading="loading">
    
    <v-toolbar v-if="selectedData" dense class="directorys-users__toolbar white elevation-0">
      <v-toolbar-title><h3>{{selectedData.label}}</h3></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn flat icon :color="edit ? 'green' : 'orange'" @click="handlerEditSave">
        <v-icon v-if="!edit">edit</v-icon>
        <v-icon v-else>save</v-icon>
      </v-btn>
    </v-toolbar>

    <v-card class="directorys-users__tree mr-2 white">      
      <el-button type="text" size="mini" icon="el-icon-circle-plus-outline" style="width:100%" @click="handlerAddNewWorker">
          
      </el-button>
      <el-menu :default-active="selectedNode" @select="handlerSelect">
        <el-menu-item v-for="user in users" :key="user.login" :index="user.login">
          <v-icon :color="user.status===3 ? 'red' : ''">account_circle</v-icon>
          <span v-if="user.status===1">{{user.surname}} {{user.name}} {{user.patronymic}}</span>
          <span v-if="user.status===2"><i>{{user.surname}} {{user.name}} {{user.patronymic}}</i></span>
          <span v-if="user.status===3" class="red--text"><del>{{user.surname}} {{user.name}} {{user.patronymic}}</del></span>
        </el-menu-item>
      </el-menu>
    </v-card>

    <v-card v-show="edit || selectedData.login" class="directorys-users__view" style="border-top: 1px solid #dfe6ec; padding:0px;">
      <v-card-title v-if="selectedData" primary-title>
        <el-form style="width:100%" ref="form" label-position="right" label-width="130px">
           <v-layout row wrap>
             <v-flex xs4>
              <el-form-item label="">
                <el-input :readonly="!edit" size="small" placeholder="" v-model="selectedData.surname"></el-input>
              </el-form-item>              
            </v-flex>
            <v-flex xs4>
              <el-form-item label="">
                <el-input :readonly="!edit" size="small" placeholder="" v-model="selectedData.name"></el-input>
              </el-form-item>              
            </v-flex>
            <v-flex xs4>
              <el-form-item label="">
                <el-input :readonly="!edit" size="small" placeholder="" v-model="selectedData.patronymic"></el-input>
              </el-form-item>              
            </v-flex>

            <v-flex xs4>
              <el-form-item label="E-Mail">
                <el-input :readonly="!edit" size="small" placeholder="E-Mail" v-model="selectedData.email"></el-input>
              </el-form-item>              
            </v-flex>
            <v-flex xs4>
              <el-form-item label=".  .">
                <el-input :readonly="!edit" size="small" placeholder="  " v-model="selectedData.cityphone"></el-input>
              </el-form-item>              
            </v-flex>
            <v-flex xs4>
              <el-form-item label=".  .">
                <el-input :readonly="!edit" size="small" placeholder="  " v-model="selectedData.internalphone"></el-input>
              </el-form-item>              
            </v-flex>

            <v-flex xs4>
              <el-form-item label="">
                <el-select :disabled="!edit" size="small" v-model="selectedData.status" placeholder=" ">
                  <el-option v-for="item in listWorkersStatus" :key="item.id" :label="item.name" :value="item.id"></el-option>
                </el-select>
              </el-form-item>              
            </v-flex>
            <v-flex xs4>
              <el-form-item label="">
                <el-select :disabled="!edit" size="small" v-model="selectedData.department" placeholder="">
                  <el-option v-for="item in listDepartments" :key="item.id" :label="item.name" :value="item.id"></el-option>
                </el-select>
              </el-form-item>              
            </v-flex>
            <v-flex xs4>
              <el-form-item label="">
                <el-select :disabled="!edit" size="small" v-model="selectedData.division" placeholder="">
                  <el-option v-for="item in listDivisions" :key="item.id" :label="item.name" :value="item.id"></el-option>
                </el-select>
              </el-form-item>
            </v-flex>

            <v-flex xs4>
              <el-form-item label="">
                <el-input :readonly="!edit" size="small" placeholder="" v-model="selectedData.login"></el-input>
              </el-form-item>
            </v-flex>
            <v-flex xs4>
              <el-form-item label="Jabber">
                <el-input :readonly="!edit" size="small" placeholder="Jabber" v-model="selectedData.jabber"></el-input>
              </el-form-item>              
            </v-flex>
            <v-flex xs4 class="py-1 pl-4">
              <el-checkbox border size="small" :disabled="!edit" v-model="selectedData.passgen">     </el-checkbox>
            </v-flex>
          </v-layout>
        </el-form>
        <el-form style="width:100%" ref="form" label-position="top" label-width="130px">     
          <v-layout row wrap>
            <v-flex xs6>
              <el-form-item label="">
                <el-transfer filterable :titles="['', ' ']" :props="transferProps" v-model="selectedData.groups" :data="listGroup"></el-transfer>
              </el-form-item>
            </v-flex>

            <v-flex xs6>
              <el-form-item label="  ">
                <el-transfer filterable :titles="['', '']" :props="transferProps" v-model="selectedData.topics" :data="listTopic"></el-transfer>
              </el-form-item>
            </v-flex>
          </v-layout>
        </el-form>        
      </v-card-title>
    </v-card>

  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "directorys-users",
  props:{
    componentId:String
  },
  data() {
    return {
      transferProps:{
        key: 'id',
        label: 'name'
      },
      selectedNode:null,
      selectedData: {
        jabber:'',
        login:'',
        surname:'',
        name:'',
        patronymic:'',
        email:'',
        cityphone:'',
        internalphone:'',
        passgen:false,
        division:null,
        department:null,
        status:1,
        groups:[],
        topics:[]
      },
      loading: false,
      edit:false,
      type:'new'
    };
  },
  computed: {
    ...mapState({
      users: state => state.directorys.users,
      listWorkers: state => state.settings.lists.workers,
      listWorkersStatus: state => state.settings.lists.statusworkers,
      listDepartments: state => state.settings.lists.department,
      listDivisions: state => state.settings.lists.division,
      theme: state => state.worker_data.workerData.theme,
      color: state => state.worker_data.workerData.color
    }),
    listGroup() {
      let data = this.$store.state.settings.lists.group
      data.forEach(element => element.disabled = !this.edit );
      return data
    },
    listTopic() {
      let data = this.$store.state.settings.lists.topic
      data.forEach(element => element.disabled = !this.edit );
      return data
    }
  },
  methods: {
    handlerSelect(id){
      let worker = this.users.find( el => el.login===id )
      this.selectedData = Object.assign({passgen:false},worker);
      this.type="update"
      this.edit=false
    },
    handlerAddNewWorker(){
      this.selectedData = {
        jabber:'',
        login:'',
        surname:'',
        name:'',
        patronymic:'',
        email:'',
        cityphone:'',
        internalphone:'',
        passgen:false,
        division:null,
        department:null,
        status:1,
        groups:[],
        topics:[]
      };
      this.type="new"
      this.edit=true
    },
    handlerEditSave(){
      if(this.edit){
        this.$vdesk.sendUpdateNoDialog(this)
        this.selectedData.passgen =false        
        this.edit=false
      } else {
        this.edit=true
      }      
    }
  }
};
</script>

<style>
.directorys-users__main {
  display: grid;
  overflow: hidden;
  height: 100%;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto 1fr;
  grid-template-areas: "directorys-users__tree directorys-users__toolbar" "directorys-users__tree directorys-users__view";
}
.directorys-users__main .el-select{
  width:100%;
}
.directorys-users__toolbar {
  grid-area: directorys-users__toolbar;
}
.directorys-users__tree {
  grid-area: directorys-users__tree;
  border-right: 1px solid #dfe6ec;
}
.directorys-users__view {
  grid-area: directorys-users__view;
  overflow: auto;
}
.el-input-group__prepend{
  width:103px;
  text-align:right;
}
</style>



