<template>
  <div class="standart-tab directorys-menu__main pa-2" v-loading="loading">
    <vd-standart-tab-toolbar @btn-click="handlerBtnClick" v-model="filterText"></vd-standart-tab-toolbar>
    <vd-standart-tab-table ref="refComponent" @dbl-click="handlerEditClick" :filter-text="filterText" :main="toTree" :data="$vdesk.filterTree(filterText,treeData,toTree.folders)" ></vd-standart-tab-table>

    <v-dialog v-model="dialogVisible" max-width="500px" :overlay="true" scrollable>
      <v-card tile>
        <vd-standart-tab-dialog-toolbar v-model="dialogVisible" @btn-click="saveDataAndClose"></vd-standart-tab-dialog-toolbar>
        <v-card-text class="h-100">
          <el-form size="small" ref="form" :model="selectedData" label-width="150px">
            <el-form-item label="">
              <el-input :disabled="selectedData.noDelete" :error="selectedDataValidationErrors.name" placeholder=" " v-model="selectedData.name"></el-input>
            </el-form-item>
            <el-form-item :label="selectedData.type==='link' ? 'URL' : ''">
              <el-input :disabled="selectedData.noDelete" :error="selectedDataValidationErrors.link" placeholder=" " v-model="selectedData.link"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select :disabled="selectedData.noDelete" filterable size="medium" v-model="selectedData._parentId" placeholder=" ">
                <el-option v-for="item in mainmenu" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="  ">
              <el-select multiple collapse-tags filterable size="medium" v-model="selectedData.groups" placeholder=" ">
                <el-option v-for="item in groupsList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-radio :disabled="selectedData.noDelete" v-model="selectedData.type" label="directory"></el-radio>
  						<el-radio :disabled="selectedData.noDelete" v-model="selectedData.type" label="link"></el-radio>
            </el-form-item>
          </el-form>
        </v-card-text>        
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "directorys-menu",
  props:{
    window:{
      type:Boolean,
      default:true
    },
    componentId:String
  },
  data() {
    return {
      toTree:{
        key:'id',
        folders:['name','link'],
        columns:[
          {prop:'name', label:'',tree:true},
          {prop:'order', label:''},
          {prop:'link', label:' / URL'}
        ]
      },
      filterText: '',
      dialogVisible:false,
      selectedDataValidationErrors:{
				name:false,
				link:false
			},
      type:'new',
      selectedData:{
				name:'',
				icon:'',
				groups:[],
        _parentId:null,
        order:null,
        link:null,
        noDelete:false,
        type:'link'
      },
      loading: false
    };
  },
  computed: {
    ...mapState({
			treeData: state => state.directorys.menu,
			groupsList: state => state.settings.lists.group,
      theme: state => state.worker_data.workerData.theme,
      color: state => state.worker_data.workerData.color
    }),
    mainmenu(){
      return this.$store.getters.getMenuDirectorys
    }
  },
  methods: {
    handlerBtnClick(type){
      switch(type){
        case 'add':
          this.handlerAddClick()
          break;
        case 'edit':
          this.handlerEditClick()
          break;
        case 'delete':
          this.handlerDeleteClick()
          break;
      }
    },
    handlerDeleteClick(){
      let node = this.$refs.refComponent.$children[0].$children[0].store.states.currentRow;
      if(node){
        if(node.noDelete){
          this.$message({type: 'error',message: '   '});
        } else {
          this.$confirm(`    - ${node.name}. ?`, 'Warning', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.selectedData = {
              id:node.id,
							icon:node.icon,
							groups:node.groups,
              name:node.name,
              _parentId:node._parentId,
              order:node.order,
              link:node.link,
              noDelete:!!node.noDelete,
              type:node.type
            };
            this.type="delete"
            this.$vdesk.sendUpdate(this)
            this.$message({type: 'success',message: ' '});
          }).catch(() => {
            this.$message({type: 'info',message: ' '});
          });
        }
      }
    },
    handlerEditClick(){
      let node = this.$refs.refComponent.$children[0].$children[0].store.states.currentRow;
      if(node){
				if(node.noDelete && node.type==='directory'){
					this.$message({type: 'error',message: '   '});
				} else {
					this.selectedData = {
						id:node.id,
						name:node.name,
						icon:node.icon,
						groups:node.groups,
						_parentId:node._parentId,
						order:node.order,
						link:node.link,
						noDelete:!!node.noDelete,
						type:node.type
					};
					this.type="update"
					this.dialogVisible=true
				}        
      }      
    },
    handlerAddClick() {
			let node = this.$refs.refComponent.$children[0].$children[0].store.states.currentRow;
			this.selectedData = {
				name:'',
				icon:'',
				groups:[],
        _parentId:node && node.type==='directory' ? node.id : null,
        order:null,
        link:null,
        noDelete:false,
        type:'link'
      }
      this.type="new"
      this.dialogVisible = true
    },
    saveDataAndClose() {
      if(!this.selectedData.name){
        this.selectedDataValidationErrors.name=true;
        setTimeout(()=>{this.selectedDataValidationErrors.name=false},2000)
      } else {
        this.$vdesk.sendUpdate(this)
      }
    }
  }
};
</script>

<style>

</style>



