<template>
  <el-menu :default-active="selectedMainTab" class="main-menu" @select="handlerSelect" :collapse="collapsed">
    <v-toolbar card dense dark :style="{background:workerData.color}" :class="workerData.theme">
      <v-tooltip bottom>
        <v-btn v-if="collapsed" icon flat @click.stop="toggleClick" slot="activator">
          <v-icon>mdi-arrow-right</v-icon>
        </v-btn>
        <v-btn v-else icon flat @click.stop="toggleClick" slot="activator">
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>        
        <span v-if="collapsed"></span>
        <span v-else></span>
      </v-tooltip>
      <!--<v-menu v-show="collapsed" offset-x right v-model="isOpenMenu">
        <v-toolbar-side-icon slot="activator"></v-toolbar-side-icon>
        <v-list dense one-line class="ma-0 pa-0">
          <v-list-tile avatar @click="">
            <v-list-tile-avatar>
              <avatar class="float:left" :username="`${ workerData.surname } ${ workerData.name } ${ workerData.patronymic }`" :size="36"></avatar>
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title> </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-divider></v-divider>
          <v-list-tile avatar @click="toggleSettings">
            <v-list-tile-avatar>
              <v-icon>mdi-settings</v-icon>
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title></v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-divider></v-divider>
          <v-list-tile avatar @click="">
            <v-list-tile-avatar>
              <v-icon>mdi-exit-to-app</v-icon>
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title></v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-divider></v-divider>
        </v-list>
      </v-menu>-->
      <v-tooltip v-show="!collapsed" bottom>
        <v-btn flat icon slot="activator">
          <!-- <v-avatar :size="36">
            <img src="http://10.72.1.253/images/43846936.jpg" />
          </v-avatar>-->
          <avatar class="float:left" :username="`${ workerData.surname } ${ workerData.name } ${ workerData.patronymic }`" :size="36"></avatar>
        </v-btn>
        <span>
          {{ workerData.surname }} {{ workerData.name }} {{ workerData.patronymic }}<br>
          : 231/234<br>
          : 30
        </span>
      </v-tooltip>
      <v-spacer v-if="!collapsed"></v-spacer>
      <nav-settings-button v-show="!collapsed" @toggle-settings="toggleSettings"></nav-settings-button>
      <v-tooltip v-show="!collapsed" bottom>
        <v-btn icon flat @click.stop="handlerLogout" slot="activator">
          <v-icon>mdi-exit-to-app</v-icon>
        </v-btn>
        <span></span>
      </v-tooltip>
      <v-spacer v-if="!collapsed"></v-spacer>
      <!-- <v-toolbar-title v-localize="{i: 'vdesk'}"></v-toolbar-title> -->
      <!--<v-spacer slot="extension"></v-spacer>
      <nav-settings-button @toggle-settings="toggleSettings" slot="extension"></nav-settings-button>
      <nav-notice-button slot="extension"></nav-notice-button>
      <v-tooltip bottom slot="extension">
        <v-btn icon flat @click.stop="handlerLogout" slot="activator">
          <v-icon>mdi-exit-to-app</v-icon>
        </v-btn>
        <span></span>
      </v-tooltip>
      <v-spacer slot="extension"></v-spacer>-->
    </v-toolbar>
    <el-submenu v-show="collapsed" index="menu.link">
      <v-icon slot="title">mdi-menu</v-icon>
      <el-menu-item index="profile">
        <v-icon>mdi-account-card-details</v-icon>
        <span slot="title"></span>
      </el-menu-item>
      <el-menu-item index="settings">
        <v-icon>mdi-settings</v-icon>
        <span slot="title"></span>
      </el-menu-item>
      <el-menu-item index="exit">
        <v-icon>mdi-exit-to-app</v-icon>
        <span slot="title"></span>
      </el-menu-item>
    </el-submenu>
    <template v-for="menu in data">      
      <el-submenu v-if="menu.children" :key="menu.id" :index="menu.link">
        <template slot="title">
          <v-icon>{{menu.icon}}</v-icon>
          <span slot="title">{{menu.name}}</span>
        </template>
        <el-menu-item v-for="item in menu.children" :key="item.id" :index="item.link">
          <v-icon>{{item.icon}}</v-icon>
          <span slot="title">{{item.name}}</span>
        </el-menu-item>
      </el-submenu>
      <el-menu-item v-else :key="menu.id" :index="menu.link">
        <v-icon>{{menu.icon}}</v-icon>
        <span slot="title">{{menu.name}}</span>
      </el-menu-item>
    </template>
  </el-menu>
</template>

<script>
import { mapState } from 'vuex'
import { Avatar } from 'vue-avatar'
export default {
  name: 'vd-sidebar',
  data() {
    return {
      isOpenMenu:false
    }
  },  
  components: {
    Avatar
  },
  computed: {
    ...mapState({
      workerData: state => state.worker_data.workerData,
      mainTabs: state => state.maintabs.mainTabs,
      collapsed: state => state.settings.sideIsCollapsed,
      selectedMainTab: state => state.maintabs.selectedMainTab
    }),
    data(){
      return this.$store.getters.getMySideMenu(this.$store.state.worker_data.workerData.groups)
    },
    hardwaresections(){
      return this.$store.getters.searchHardwareDirectorys
    }
  },
  methods: {
    toggleClick() {
      if (this.$store.state.settings.sideIsCollapsed) {
        this.$store.dispatch('sideExpand');
      } else {
        this.$store.dispatch('sideCollapse');
      }
    },
    toggleSettings(){
       this.$emit('toggle-settings')
    },
    handlerSelectA({key,item:{title}}){
    //  console.log(item)
      console.log(title)
      let isValue = true;
      this.mainTabs.forEach((elem, idx) => {
        if (elem.name === key) { isValue = false; }
      })
 
      if (isValue) {
        this.$store.dispatch('addMainTab', {
          title: title,
          name: key,
          closable: true,
          isLink:key.startsWith('http')
        });
      }
      this.$store.dispatch('setSelectedMainTab', key);
      
    },
    handlerSelect(id, arrId, { $el: { textContent: title } }) {
      let isValue = true;
      let isLink = id.startsWith('http')
      
      this.mainTabs.forEach((elem, idx) => {
        if (elem.name === id) { isValue = false; }
      })
      if(isLink){
        this.data.forEach( el => {
          if(el.link === id){ title = el.name }
        })
      }
      if (isValue) {
        this.$store.dispatch('addMainTab', {
          title: title,
          name: id,
          closable: true,
          isLink:isLink
        });
      }
      this.$store.dispatch('setSelectedMainTab', id);
    }
  }
}
</script>

<style>
#main-layout > aside > .ant-layout-sider{
  height:100vh;
}
#main-layout > aside > .ant-layout-sider > .ant-layout-sider-children > .ant-menu{
  /*height:100vh;*/
  overflow: auto;
}

ul.main-menu {
  height: 100%;
}
ul.main-menu:not(.el-menu--collapse) {
  width: 250px;
}
ul.main-menu .toolbar__content>:not(.btn):not(.menu):first-child:not(:only-child), ul.main-menu .toolbar__extension>:not(.btn):not(.menu):first-child:not(:only-child){
  margin-left: 1px;
}
ul.main-menu .toolbar__content>:not(.btn):not(.menu):last-child:not(:only-child), ul.main-menu .toolbar__extension>:not(.btn):not(.menu):last-child:not(:only-child){
  margin-right: 1px;
}
/*.el-menu--collapse .toolbar__content{
  height:96px !important;
  flex-wrap: wrap;
  justify-content: center;
}*/
</style>



