<template>
  <v-dialog ref="dialog" v-model="dialogVisible" :fullscreen="dialogMaximize" :max-width="maxWidth" persistent :hide-overlay="!overlay" scrollable>
      <v-card tile>
        <slot name="toolbar">
          <vd-standart-tab-dialog-toolbar title="  " @btn-minimize="dialogMinimize = !dialogMinimize" :minimize="dialogMinimize" @btn-maximize="dialogMaximize = !dialogMaximize" :maximize="dialogMaximize" v-model="dialogVisible" @btn-click="handlerBtnClick"></vd-standart-tab-dialog-toolbar>
        </slot>        
        <v-card-text v-show="!dialogMinimize" class="h-100">
          <slot></slot>
        </v-card-text>        
      </v-card>
    </v-dialog>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "vd-standart-tab-dialog",
  props:{
    value:Boolean,
    overlay:{
      type:Boolean,
      default:false
    },
    maxWidth:{
      type:String,
      default:"600px"
    }
  },
  data(){
    return {
      dialogMaximize:false,
      minimize:false,
      id:this.$vdesk.randomGenerator(16)
    }
  },
  computed: {
    dialogMinimize:{
      get(){return this.minimize},
      set(val){
        this.minimize = val
        if(val){
          this.$store.commit("ADD_MINIMIZED_WINDOW", {id:this.id, wind:{id:this.id,title:'qweasd'}});
        } else {
          this.$store.commit("DELETE_MINIMIZED_WINDOW", this.id);
        }        
      }
    },
    dialogVisible:{
      get(){return this.value;},
      set(val){this.$emit('input', val)}
    }
  },
  methods:{
    handlerBtnClick(){
      this.$emit('btn-click')
    }
  }
};
</script>

<style>

</style>


