<template>
  <v-app v-loading.fullscreen="loading" element-loading-background="rgba(0, 0, 0, 0.8)" element-loading-spinner="el-icon-loading" :element-loading-text="loadingLabel" toolbar>

    <layout v-if="isLogged && !loading">
      <!--<vd-navbar @toggle-settings="settings.settingsIsShow = !settings.settingsIsShow" slot="toolbar"></vd-navbar>-->
      <vd-sidebar @toggle-settings="settings.settingsIsShow = !settings.settingsIsShow" slot="aside"></vd-sidebar>
          
      <vd-maintabs slot="main"></vd-maintabs>
      <!--<vd-footer slot="footer"></vd-footer>-->
    </layout>

    <div v-if="isLogged && !loading" class="ma-3" style="position:fixed; top:0px; right:0px; ">
      <v-notice-button v-if="isLogged && !loading"></v-notice-button>
      <nav-addressbook-button></nav-addressbook-button>
      <v-btn color="pink--text" dark icon @click="showCreateTiketDialog = true">
        <v-icon large>mdi-plus-circle-outline</v-icon>
      </v-btn>
    </div>

    <!--<v-notice-button v-if="isLogged && !loading"></v-notice-button>
    <v-btn color="pink" dark small fixed top right fab @click="showCreateTiketDialog = true">
      <v-icon>add</v-icon>
