    <v-dialog v-if="isLogged && !loading" v-model="showCreateTiketDialog" max-width="500px" overlay scrollable>
      <v-card tile>
        <v-toolbar dark dense :class="['elevation-0',workerData.theme]" :style="{background:workerData.color}">
          <v-toolbar-items v-show="!createTicketMessage">      
            <v-btn flat @click="createTicket">
              <v-icon left>mdi-plus-circle</v-icon>
               
            </v-btn>
          </v-toolbar-items>
          <v-spacer></v-spacer>
          <v-btn icon dark @click.native="closeCreateTiketDialog">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text v-if="createTicketMessage" class="text-md-center display-1">
            
          <h1 class="red--text display-3">{{createTicketId}}</h1>
        </v-card-text>
        <v-card-text v-else>
          <el-form size="small" ref="form" :model="createTicketData" label-width="150px">
            <el-form-item label="">
              <el-select filterable size="medium" v-model="createTicketData.worker" placeholder=" ">
                <el-option v-for="item in listWorkers" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label=" ">
              <el-date-picker v-model="createTicketData.deadline" format="dd.MM.yyyy HH:mm:ss" type="datetime" placeholder="Select date and time"></el-date-picker>
            </el-form-item>
            <el-form-item label="">
              <el-cascader :props="classifierProps" separator="." filterable expand-trigger="hover" :options="classifierData" v-model="createTiketFormClassifier"></el-cascader>
            </el-form-item>
            <el-form-item label="">
              <el-input type="textarea" placeholder=" " v-model="createTicketData.description"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="createTiketFormCompleeted" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
            </el-form-item>            
            <el-form-item label="(.)">
              <el-input-number :disabled="!createTiketFormCompleeted" v-model="createTicketData.laborexpenditures" :min="1"></el-input-number>
            </el-form-item>
          </el-form>
        </v-card-text>        
      </v-card>
    </v-dialog>

    <!--<v-speed-dial v-if="isLogged && !loading" fixed v-model="fab" bottom right direction="left">
        
        <v-btn slot="activator" color="blue darken-2" dark fab hover v-model="fab">
          <v-icon>account_circle</v-icon>
          <v-icon>close</v-icon>
        </v-btn>
        <v-tooltip top>
          <v-btn  fab dark small color="red" slot="activator">
            <v-icon>mdi-exit-to-app</v-icon>
          </v-btn>
          <span></span>
        </v-tooltip>
        <v-tooltip top>
          <v-btn  fab dark small color="indigo" slot="activator" @click="settings.settingsIsShow = !settings.settingsIsShow">
            <v-icon>mdi-settings</v-icon>
          </v-btn>
          <span></span>
        </v-tooltip>
        <v-tooltip top>
          <v-btn  fab dark small color="indigo" slot="activator">
            <v-icon>account_circle</v-icon>
          </v-btn>
          <span></span>
        </v-tooltip>
        <v-tooltip top>
          <v-btn  fab dark small color="green" slot="activator">
            <v-icon>add</v-icon>
          </v-btn>
          <span> </span>
        </v-tooltip>
        
    </v-speed-dial>-->

    <!--<v-dialog v-if="isLogged && !loading" v-model="settings.settingsIsShow">
        123
    </v-dialog>-->

    <!--<div class="vd-minimized-area">
      {{ minimizedWindows }}
      23424
      2342
      42
      4
      24234
      <template v-for="wind in minimizedWindows">
        <el-tooltip :key="wind.id" class="item" effect="dark" :content="wind.title" placement="top-start">
          <v-btn dark class="red">
            <v-icon>mdi-window-restore</v-icon>
          </v-btn>
        </el-tooltip>
      </template>
    </div>-->

    <v-dialog v-if="isLogged && !loading" v-model="settings.settingsIsShow" max-width="500px" :overlay="true" scrollable>
        <v-card tile>
          <v-toolbar dark dense :class="['elevation-0',workerData.theme]" :style="{background:workerData.color}">
            <v-toolbar-title v-localize="{i: 'settings'}"></v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn icon dark @click.native="settings.settingsIsShow = false">
              <v-icon>close</v-icon>
            </v-btn>
          </v-toolbar>
          <v-card-text>
            <table>
              <tr>
                <td class="pa-2">
                  <v-switch style="height: 30px;" :label="isColor ? '' : ''" v-model="isColor"></v-switch>
                </td>
                <td class="pl-4">
                  <el-color-picker v-if="isColor" color-format="hex" v-model="colorApp"></el-color-picker>
                  <v-menu v-else offset-y :max-width="270" :close-on-content-click="false" v-model="settings.colorMenuOpen">
                    <v-btn :color="themeApp" dark slot="activator"></v-btn>
                    <v-card>
                      <v-container fluid grid-list-lg text-xs-center>
                        <v-tooltip v-for="theme in themeList" :key="theme" top>
                          <v-chip slot="activator" :selected="themeApp===theme" :class="[theme==='default' ? 'grey lighten-4' : theme,theme==='default' ? 'black--text' : theme+'--text']" @click="setTheme(theme)" outline>
                            <v-icon v-if="themeApp===theme">check_circle</v-icon>
                            <v-icon v-else>cancel</v-icon>
                          </v-chip>
                          <span>{{theme}}</span>
                        </v-tooltip>                      
                      </v-container>
                    </v-card>
                  </v-menu>
                </td>
              </tr>
              <tr>
                <td class="pa-2" v-localize="{i: 'locationListRequests'}"></td>
                <td class="pl-4">
                  <v-btn-toggle v-model="viewApp">
                    <v-btn v-for="item in viewList" :key="item.type" :value="item.type" large>
                      <v-tooltip top>
                        <v-icon slot="activator" large>{{item.icon}}</v-icon>
                        <span>{{item.name}}</span>
                      </v-tooltip>
                    </v-btn>
                  </v-btn-toggle>
                </td>
              </tr>
            </table>
          </v-card-text>
        </v-card>
    </v-dialog>

    <v-container v-if="!isLogged && !loading" class="login" grid-list-md text-xs-center>     
      <v-card>
        <v-card-title primary-title>
          <h3 class="headline mb-0" v-localize="{i: 'vdesk'}"></h3>
        </v-card-title>
        <v-card-text>
          <!-- <v-text-field :class="{'red--text':formAddons.usernameError}" :error-messages="formAddons.usernameErrorMsg" :label="$locale({i: 'username'})" v-model="formAddons.username" prepend-icon="person" @keyup.enter.prevent="handleSubmit"></v-text-field>-->
          <v-text-field :class="{'red--text':formAddons.passwordError}" :error-messages="formAddons.passwordErrorMsg" :label="$locale({i: 'password'})" v-model="formData.password" prepend-icon="mdi-lock" :append-icon="formAddons.passwordShow ? 'mdi-eye' : 'mdi-eye-off'" :type="formAddons.passwordShow ? 'text' : 'password'" :append-icon-cb="() => (formAddons.passwordShow = !formAddons.passwordShow)" @keyup.enter.prevent="handleSubmit"></v-text-field>
          <v-checkbox label=" " v-model="formData.saveMe" light></v-checkbox>
        </v-card-text>
        <v-card-actions>
          <v-btn flat class="orange--text" @click="handleSubmit" v-localize="{i: 'enter'}"></v-btn>
        </v-card-actions>
      </v-card>
    </v-container>
  </v-app>
</template>
<script>
import { mapState } from "vuex"
import moment from 'moment'
export default {
  data() {
    return {
      classifierProps:{
        label:"name",
        value:"id"
      },
      showCreateTiketDialog:false,
      createTicketMessage:false,
      createTicketId:null,
      fab:false,
      createTicketData:{
        worker: null,
        description:null,
        deadline:moment().add(1, 'days'),
        classifier: null,
        laborexpenditures: 0,
        status:3
      },
      createTiketFormCompleeted:false,
      createTiketFormClassifier:[],
      settings:{
        colorMenuOpen:false,
        settingsIsShow:false
      },
      usernamesList:[],
      settingsTabSelected:'tab-appearance',      
      isColor:false,
      loadingLabel: "...",
      formData:{
        saveMe:false,
        password: "",
        vDeskSave:null
      },
      formAddons: {
        passwordError: false,
        passwordErrorMsg:'',
        passwordShow: false
      }
    };
  },
  computed: {
    colorApp: {
      get() {
        return this.$store.state.worker_data.workerData.color;
      },
      set(value) {
        this.$store.commit("SET_COLOR", value);
        this.setWorkerSetting("color", value);
      }
    },
    themeApp: {
      get() {
        return this.$store.state.worker_data.workerData.theme;
      },
      set(value) {        
        this.$store.commit("SET_THEME", value);
        this.setWorkerSetting("theme", value);
      }
    },
    viewApp: {
      get() {
        return this.$store.state.worker_data.workerData.tiketsview;
      },
      set(value) {
        this.$store.commit("SET_TIKETS_SHOW_VIEW", value);
        this.setWorkerSetting("tiketsview", value);
      }
    },
    workerData(){
      let data = this.$store.state.worker_data.workerData
      if(data){
        this.createTicketData.worker = data.login
        return data
      }
      return {}
    },
    ...mapState({
      classifierData: state => state.directorys.classifier,
      themeList: state => state.settings.themeList,
      themeShades: state => state.settings.thmeeShades,
      minimizedWindows: state => state.settings.minimizedWindows,
      viewList: state => state.tikets.viewList,
      isAdmin: state => state.worker_data.isAdmin,
      isLogged: state => state.worker_data.isLogged,
      sideMenu: state => state.directorys.menu,
      listWorkers: state => state.settings.lists.workers,
      loading: state => state.settings.loading
    }),
    settingsButtonStyle() {
      return {
        right: this.settingsShow ? "289px" : "-11px",
        "box-shadow":
          "0 -5px 8px -2px rgba(0,0,0,.2),0 -5px 4px -2px rgba(0,0,0,.14),0 3px 3px -2px rgba(0,0,0,.12) !important"
      };
    }
  },
  created() {
    this.$language = "ru";
  },
  mounted() {
    let vDeskSave = this.getCookie('vDeskSave')
    if(vDeskSave && !this.$store.state.socket.connect){
      this.formData.vDeskSave = vDeskSave
      this.sendLoginData();
    }
    this.$store.subscribe((mutation, state) => {
      if (mutation.type === "SOCKET_ERROR") {
        this.$store.dispatch('stopLoading')
        this.$socket.close();
        if(vDeskSave){
          this.$socket.open();
        }
      }
      if (mutation.type === "SOCKET_DISCONNECT") {
        window.open("/", "_self");
        localStorage.clear();
      }
    });
  },
  methods: {
    clearCreateTiketDialog(){
      this.createTicketData.worker = this.workerData.login
      this.createTicketData.description = null
      this.createTicketData.deadline = moment().add(1, 'days')
      this.createTicketData.classifier = []
      this.createTiketFormClassifier = []
      this.createTicketData.laborexpenditures = 0
      this.createTiketFormCompleeted = false
    },
    closeCreateTiketDialog(){
      this.showCreateTiketDialog = false
      this.createTicketMessage = false
      this.clearCreateTiketDialog()
    },
    createTicket(){
      let self = this
      let addingData = {
        sendername:this.workerData.name,
        sendersurname:this.workerData.surname,
        senderpatronymic:this.workerData.patronymic,
        sendermail:this.workerData.email,
        senderphone:this.workerData.internalphone,
        topicname:" ",
        sender:this.workerData,
        isMine: this.workerData.login === this.createTicketData.worker
      }
      this.createTicketData.status = this.createTiketFormCompleeted ? 1 : 3
      this.createTicketData.new = !this.createTiketFormCompleeted
      this.createTicketData.classifier = this.createTiketFormClassifier.pop()
      let sendData = Object.assign(addingData, this.createTicketData);
      this.$http.post(`/sendtiketself`,sendData).then(response => {
        console.log(response)
        self.createTicketMessage = true
        self.createTicketId = response.body.id
        this.clearCreateTiketDialog()
      });
    },
    setWorkerSetting(key, value) {
      this.$http.post(`/setWorkerSettings`, {login: this.workerData.login,key: key,value: value});
    },
    setTheme(value) {
      this.$store.commit("SET_THEME", value);
      this.setWorkerSetting("theme", value);
    },
    getCookie(name) {
      var matches = document.cookie.match(new RegExp(
        "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
      ));
      return matches ? decodeURIComponent(matches[1]) : undefined;
    },
    setCookie(name, value, options) {
      options = options || {};
      let expires = options.expires;
      if (typeof expires == "number" && expires) {
        let d = new Date();
        d.setTime(d.getTime() + expires * 1000);
        expires = options.expires = d;
      }
      if (expires && expires.toUTCString) {
        options.expires = expires.toUTCString();
      }
      value = encodeURIComponent(value);
      let updatedCookie = `${name}=${value}`;
      for (let propName in options) {
        updatedCookie += `;${propName}`;
        let propValue = options[propName];
        if (propValue !== true) {
          updatedCookie += `=${propValue}`;
        }
      }
      document.cookie = updatedCookie;
    },
    clearErrorsStatuses(){
      setTimeout(()=>{
        this.formAddons.passwordError=false
        this.formAddons.passwordErrorMsg=''
      },2000)
    },
    sendLoginData(){
      this.$store.dispatch('startLoading')
      this.$http.post('/login', this.formData).then(( {body:data} ) => {
        if (data.status) {
          if(data.vDeskSave && !this.formData.vDeskSave){
            this.setCookie('vDeskSave', data.vDeskSave, {path:'/',expires:moment().add(30, 'days')})
          }
          this.$socket.open();

        } else {
          this.$store.dispatch('stopLoading')
          this.formAddons.passwordError=true
          this.formAddons.passwordErrorMsg=data.msg
          this.clearErrorsStatuses()
        }
      });
    },
    handleSubmit() {
      if (this.formData.password) {
        this.sendLoginData();
      } else {
        this.formAddons.passwordError=true
        this.formAddons.passwordErrorMsg='    '
        this.clearErrorsStatuses()
      }
    }
  }
}
</script>

<style>
.vd-minimized-area{
  position:fixed;
  left:0px;
  bottom:0px;
  display:flex;
}

.badge--overlap::after {
  top: -4px !important;
}
.login {
  position: absolute !important;
  width: 400px !important;
  height: 330px !important;
  top: 50% !important;
  left: 50% !important;
  margin-top: -165px !important;
  margin-left: -200px !important;
}
.btn-settings {
  position: fixed !important;
  margin: 0px;
  min-width: 50px;
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
  top: 48px;
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
  z-index: 6;
}
.btn-settings > .btn__content {
  padding: 0px;
}
.toolbar-mini  .toolbar__content{
  height:28px !important;
  padding-left:8px !important;
}
.toolbar-mini  .toolbar__content .btn  .btn__content{
  padding: 6px !important;
}
.toolbar-mini  .toolbar__content  .btn,
.toolbar-mini  .toolbar__content .btn  .btn__content,
.toolbar-mini  .toolbar__content .btn--icon  .btn__content{
  height:24px !important;  
}
.toolbar-mini  .toolbar__content .btn--icon  .btn__content,
.toolbar-mini  .toolbar__content .btn  .btn__content  .icon{
  font-size:18px !important;
}
.toolbar-mini  .toolbar__content .btn  .btn__content{
  vertical-align: bottom;
  line-height: 18px;
  font-size:12px !important;
}
.toolbar-mini  .toolbar__content .btn  .btn__content  span{
  font-size:12px !important;
  line-height: 12px;
  
}
.toolbar-mini  .toolbar__content  .btn:not(.btn--icon){  
  margin: 2px !important;
}
.toolbar-mini  .toolbar__content  .btn--icon{
  width:24px !important;
  margin: 5px !important;
}
.toolbar-mini  .toolbar__content  .btn .icon--left{  
  margin-right: 5px !important;
}
[error] > input, [error] > textarea, .el-table[error] {
  color: red;
  border: 1px solid red;
}
[error] > input, [error] > textarea, .el-table[error] {
  color: red;
  border: 1px solid red;
}
.inverted{
  filter: invert(100%);
}
</style>



