export default {
  state: {
    toolbarsClasses:"white elevation-1 mb-2",
    mainTabs: [/*{
      title: 'Dashboard',
      name: 'dashboard',
      closable: false
    },*/ {
      title: ' ',
      name: 'tikets-my',
      closable: true
    }/*, {
      title: ' ',
      name: 'tikets-all',
      closable: true
    }*/],
    selectedMainTab: 'tikets-my',//'dashboard'
    openSubMenu:'tikets'
  },
  actions: {
    setMainTabs({commit}, tabs) {
      commit('SET_MAIN_TABS', tabs)
    },
    addMainTab({commit}, tab) {
      commit('ADD_MAIN_TAB', tab)
    },
    setSelectedMainTab({commit}, id) {
      commit('SET_SELECTED_MAIN_TAB', id)
    },
    deleteMainTab({dispatch,commit}, id) {
      commit('DELETE_MAIN_TAB', {id,dispatch})
    }
  },
  mutations: {
    SET_MAIN_TABS(state, tabs) {
      state.mainTabs = tabs;
    },
    ADD_MAIN_TAB(state, tab) {
      state.mainTabs.push(tab)
    },
    SET_SELECTED_MAIN_TAB(state, id) {
      state.selectedMainTab = id;
    },
    DELETE_MAIN_TAB(state, {id,dispatch}) {
      let idx = state.mainTabs.findIndex((elem, idx) => {
        return elem.name === id;
      });
      state.mainTabs.splice(idx, 1);
      dispatch('setSelectedMainTab', state.mainTabs[state.mainTabs.length - 1].name)
    }
  }
}



