<template>
  <div class="standart-tab shared-invoices__main pa-2">
    <vd-standart-tab-toolbar print-btn @btn-click="handlerBtnClick" v-model="filterText"></vd-standart-tab-toolbar>
    <vd-standart-tab-table ref="refComponent" @dbl-click="handlerRowDblClick" :filter-text="filterText" :main="toTree" :data="$vdesk.filterTree(filterText,treeData,toTree.folders)" ></vd-standart-tab-table>

    <v-dialog v-model="dialogVisible" max-width="900px" transition="dialog-bottom-transition" :overlay="true" scrollable>
      <v-card class="shared-invoices__main-dialog" tile>
        <v-toolbar dense dark :class="['elevation-0',theme]" :style="{background:color}">
          <v-toolbar-items>
            <v-btn flat :disabled="!edit" @click="saveData">
              <v-icon left>save</v-icon>
              
            </v-btn>          
            <v-btn flat :disabled="!edit" @click="saveDataAndClose">
              <div>
                <v-icon>save</v-icon>
                <v-icon>arrow_forward</v-icon>
                <v-icon>close</v-icon>
              </div>              
                
            </v-btn>
            <v-btn flat :disabled="edit" @click="handlerPrint">
              <v-icon left>print</v-icon>
              
            </v-btn>
          </v-toolbar-items>
          <v-spacer></v-spacer>
          <v-btn icon dark @click.native="dialogVisible = false">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text class="h-100">
          <table class="w-100  align-right" style="border-spacing:8px;">
            <tr>
              <td class="subheading">:</td>
              <td>
                <el-date-picker size="mini" value-format="yyyy-MM-dd" format="dd.MM.yyyy" :error="selectedDataValidationErrors.date" :disabled="!edit" class="w-100" v-model="selectedData.date" placeholder="  "></el-date-picker>
              </td>
              <td>
                <el-select size="mini" filterable :disabled="!edit" v-model="selectedData.document_type" placeholder="  " class="w-100">
									<el-option v-for="item in doctypeList" :key="item.id" :label="item.name" :value="item.id"></el-option>
                </el-select>
              </td>
              <td>
                <el-input size="mini" :error="selectedDataValidationErrors.reason" :disabled="!edit" placeholder="  " v-model="selectedData.reason"></el-input>
              </td>
            </tr>
            <tr>
              <td class="subheading">:</td>
              <td>
                <vd-select @click="btnClick('department')" :error="selectedDataValidationErrors.department" :disabled="!edit" :list="departmentList" v-model="selectedData.department" placeholder=" "></vd-select>
              </td>
              <td class="subheading">:</td>
              <td>
                <vd-select @click="btnClick('contractor')" :error="selectedDataValidationErrors.contractor" :disabled="!edit" v-model="selectedData.contractor" placeholder=" " :list="contractorList"></vd-select>
              </td>
            </tr>
            <tr>
              <td class="subheading"> :</td>
              <td>
                <vd-select @click="btnClick('released_jobtitle')" :error="selectedDataValidationErrors.released_jobtitle" :disabled="!edit" v-model="selectedData.released_jobtitle" placeholder=" " :list="jobtitleList" component="directorys-jobtitle"></vd-select>
              </td>
              <td class="subheading"> :</td>
              <td>
                <vd-select @click="btnClick('received_jobtitle')" :error="selectedDataValidationErrors.received_jobtitle" :disabled="!edit" v-model="selectedData.received_jobtitle" placeholder=" " :list="jobtitleList" component="directorys-jobtitle"></vd-select>
              </td>
            </tr>
            <tr>
              <td class="subheading">:</td>
              <td><el-input size="mini" :error="selectedDataValidationErrors.released_fio" :disabled="!edit" v-model="selectedData.released_fio"></el-input></td>
              <td class="subheading">:</td>
              <td><el-input size="mini" :error="selectedDataValidationErrors.received_fio" :disabled="!edit" v-model="selectedData.received_fio"></el-input></td>
            </tr>
          </table>          
          <div class="shared-invoices__dialog_equipment">
            <v-toolbar dark dense :class="['elevation-0',theme]">
              <v-toolbar-items>
                <v-btn flat :disabled="!edit" @click="addDialogVisible=true">
                  <v-icon left>add_circle_outline</v-icon>
                  
                </v-btn>
              </v-toolbar-items>
            </v-toolbar>
            <el-table :error="selectedDataValidationErrors.equipments" size="mini" sum-text="" show-summary :summary-method="getSummaries"  highlight-current-row :data="selectedData.equipments">
              <el-table-column align="left" prop="name" label="" width="auto"></el-table-column >
              <el-table-column align="right" header-align="right" prop="quantity" label="" width="100">
                <template slot-scope="scope">
                  {{ scope.row.quantity }} {{ $locale({i: 'numberAbbr'}) }}
                </template>
              </el-table-column>
              <el-table-column align="right" header-align="right" prop="withoutVAT" label="  " width="120">
                <template slot-scope="scope">
                  {{modeNumber(scope.row.withoutVAT)}}
                </template>
              </el-table-column>
              <el-table-column align="right" header-align="right" prop="price" label="" width="120">
                <template slot-scope="scope">
                  {{modeNumber(scope.row.price)}}
                </template>
              </el-table-column>
              <el-table-column align="right" header-align="right" prop="total" label="" width="120">
                <template slot-scope="scope">
                  {{modeNumber(scope.row.total)}}
                </template>
              </el-table-column>
            </el-table>
          </div>
        </v-card-text>        
      </v-card>
    </v-dialog>

		<v-dialog v-model="addDialogVisible"  max-width="403px" :overlay="true" scrollable>
    	<v-card tile>
        <v-toolbar dark dense :class="['elevation-0',theme]" :style="{background:color}">
          <v-toolbar-items>
            <v-btn flat @click="addSelectedDataEquipments">
              <v-icon left>save</v-icon>
                
            </v-btn>
          </v-toolbar-items>
					<v-spacer></v-spacer>
					<v-btn icon dark @click.native="addDialogVisible = false">
						<v-icon>close</v-icon>
					</v-btn>
      	</v-toolbar>
				<v-card-text class="h-100">
					<table class="w-100 align-right">
						<tr>
							<td class="subheading">:</td>
							<td>
                <vd-select @click="btnClick('hardware')" :error="addEquipmentNameError" :disabled="!edit" v-model="addEquipmentName" placeholder=" " :list="hardwareitemsList"></vd-select>
							</td>
						</tr>
						<tr>
							<td class="subheading">:</td>
							<td>
								<el-input :error="addEquipmentQuantityError" size="mini" placeholder=" " v-model="addEquipmentQuantity"></el-input>
							</td>
						</tr>
						<tr>
							<td class="subheading">:</td>
							<td>
								<el-input :error="addEquipmentTotalError" size="mini" placeholder="  " v-model="addEquipmentTotal"></el-input>
							</td>
						</tr>
						<tr>
							<td class="subheading">:</td>
							<td>{{ modeNumber(addEquipmentPrice) }}</td>
						</tr>
						<tr>
							<td class="subheading">  :</td>
							<td>{{ modeNumber(addEquipmentWithoutVAT) }}</td>
						</tr>
					</table>
				</v-card-text>
			</v-card>
		</v-dialog>

    <v-dialog v-model="itemDialogVisible" max-width="703px" :overlay="true" scrollable>
      <v-card tile>
        <v-toolbar dark dense :class="['elevation-0',theme]" :style="{background:color}">
          <v-btn flat icon @click="getEquipmentFromList">
            <v-icon>check</v-icon>
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn icon dark @click.native="itemDialogVisible = false">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text class="pt-0">
          <component :is="component" :component-id="component" ref="component"></component>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import moment from "moment";
import { mapState } from "vuex";
import numeral from "numeral";
import "numeral/locales/ru.js";
numeral.locale("ru");
export default {
  name: "shared-invoices",
  props:{
    componentId:String
  },
  data() {
    let vue = this;
    return {
      toTree:{
        key:'id',
        folders:['contractorname','date','departmentname','id','reason','received_fio','released_fio','total_count','total_sum'],
        columns:[
          {
            prop:'date',
            label:'',
            width:150,
            tree:true,
            render: (scope) => scope.row.isItem ? vue.modeDate(scope.row.date,'DD.MM.YYYY') : scope.row.date
          },
          {
            prop:'id',
            label:'',
            width:50,
            render: (scope) => scope.row.isItem ? scope.row.id : ''
          },
          {
            prop:'reason',
            label:''
          },
          {
            prop:'contractorname',
            label:'',
            width:250
          },
          {
            prop:'departmentname',
            label:''
          },
          {
            prop:'released_fio',
            label:'',
            width:250
          },
          {
            prop:'received_fio',
            label:'',
            width:250
          },
          {
            prop:'total_count',
            label:'',
            render: (scope) => scope.row.isItem ? `${ scope.row.total_count } ${ vue.$locale({i: 'numberAbbr'}) }` : ''
          },
          {
            prop:'total_sum',
            label:'',
            render: (scope) => scope.row.isItem ? vue.modeNumber(scope.row.total_sum) : ''
          }
        ]
      },
      folders:['contractorname','date','departmentname','id','reason','received_fio','released_fio','total_count','total_sum'],
      component:null,
      selectType:'',
      filterText:'',
      dialogVisible: false,
      addDialogVisible: false,
      itemDialogVisible: false,
      edit: false,
      addEquipmentName: null,
      addEquipmentNameError: false,
      addEquipmentQuantity: null,
      addEquipmentQuantityError: false,
      addEquipmentTotal: null,
      addEquipmentTotalError: false,
      doctypeList: [
        { id: 1, name: " " },
        { id: 2, name: "-" }
      ],
      selectedData: {
        date: null,
        reason: "",
        document_type: 1,
        department: null,
        contractor: null,
        released_jobtitle: 18,
        released_fio: "  ",
        received_jobtitle: 8,
        received_fio: "  ",
        equipments: []
      },
      selectedDataValidationErrors: {
        date: false,
        reason: false,
        department: false,
        contractor: false,
        released_jobtitle: false,
        released_fio: false,
        received_jobtitle: false,
        received_fio: false,
        equipments: false
      }
    };
  },
  computed: {
    ...mapState({
      treeData: state => state.shared.invoices,
      jobtitleList: state => state.directorys.jobtitle,
      departmentList: state => state.directorys.departments,
      contractorList: state => state.directorys.contractors,
      //hardwareitemsList: state => state.settings.lists.hardwareitems,//getHardwareItems
      theme: state => state.worker_data.workerData.theme,
      color: state => state.worker_data.workerData.color
    }),
    hardwareitemsList(){
      return this.$store.getters.getHardwareItems
    },    
    addEquipmentPrice() {
      return this.addEquipmentTotal ? this.addEquipmentTotal / this.addEquipmentQuantity : "";
    },
    addEquipmentWithoutVAT() {
      return this.addEquipmentPrice ? +(this.addEquipmentPrice / 1.18).toFixed(2) : "";
    }
  },
  methods: {
    handlerBtnClick(type){
      switch(type){
        case 'add':
          this.handlerAddClick()
          break;
        case 'edit':
          this.handlerEditClick()
          break;
        case 'delete':
          this.handlerDeleteClick()
          break;
        case 'print':
          this.handlerPrint()
          break;
      }
    },
    getSummaries(params) {
      const { columns, data } = params;
      if (this.edit) {
        let count = 0
        let sum = 0        
        data.forEach((el, idx) => {
          count += el.quantity;
          sum += el.total;
        });
        this.selectedData.total_count = count;
        this.selectedData.total_sum = sum;
      }
      return ["",`${this.selectedData.total_count} .`,,,this.modeNumber(this.selectedData.total_sum)]; //sums;
    },
    handlerAddClick() {
      this.edit = true;
      this.selectedData = {
        date: null,
        reason: "",
        document_type: 1,
        department: null,
        contractor: null,
        released_jobtitle: 18,
        released_fio: "  ",
        received_jobtitle: 8,
        received_fio: "  ",
        total_count: 0,
        total_sum: 0,
        equipments: []
      };
      this.dialogVisible = true;
      this.type='new'
    },
    handlerEditClick() {
      if (this.$refs.refComponent.$children[0].$children[0].store.states.currentRow.isItem) {
        this.handlerRowDblClick(
          this.$refs.refComponent.$children[0].$children[0].store.states.currentRow
        );
        this.edit = true;
        this.type='update'
      }
    },
    handlerRowDblClick(row, event) {
      if (row.isItem) {
        this.selectedData = Object.assign({}, row);
        this.selectedData.equipments = [].concat(row.equipments);
        this.dialogVisible = true;
      }
    },
    modeDate(data, format) {
      return moment(data).format(format);
    },
    modeNumber(num, format = "0,0.00 $") {
      return numeral(+num).format(format);
    },
    addSelectedDataEquipments() {
      let isValid=true
      let keys=['addEquipmentName','addEquipmentQuantity','addEquipmentTotal']
      keys.forEach(el => {
        isValid=this[el]
        this[`${el}Error`]=!this[el];
        if(this[`${el}Error`]){
          setTimeout(()=>{this[`${el}Error`]=!this[`${el}Error`]},2000)
        }
      })
      if(isValid){
        let name = this.getListName(this.hardwareitemsList,this.addEquipmentName);
        this.selectedData.equipments.push({
          depth:2,
          name: name,
          invoice:this.selectedData.id,
          equipment: this.addEquipmentName,
          quantity: +this.addEquipmentQuantity,
          total: +this.addEquipmentTotal,
          price: +this.addEquipmentPrice,
          withoutVAT: +this.addEquipmentWithoutVAT
        });
        this.addEquipmentName = null;
        this.addEquipmentQuantity = null;
        this.addEquipmentTotal = null;
        this.addDialogVisible = false;
      }      
    },
    btnClick(value){
      switch(value){
        case 'department':
          this.component = 'directorys-departments'
          break
        case 'contractor':
          this.component = 'directorys-contractors'
          break
        case 'released_jobtitle':
        case 'received_jobtitle':
          this.component = 'directorys-jobtitle'
          break
        case 'hardware':
          this.component = 'directorys-hardware'
          break
      }
      this.selectType=value
      this.itemDialogVisible = true
    },
    getEquipmentFromList() {
      let node = this.$refs.component.$refs.refComponent.store.states.currentRow;
      if(node){
        if(this.selectType === 'hardware' && node.isItem){
          this.addEquipmentName = node.id
        } else {
          this.selectedData[this.selectType] = node.id
        }
        this.itemDialogVisible = false;
        this.$refs.component.filterText = "";
      }
    },
    getListName(list, id) {
      let item = list.find(el => {
        return el.id === id;
      });
      return item ? item.name : "";
    },
    saveDataAndClose() {
      if(this.saveData()){
        this.dialogVisible = false
      }      
    },
    saveData() {
      let isValid = true;
      let keys = Object.keys(this.selectedData);
      keys.forEach(el => {
        if (el === "equipments" && !this.selectedData[el].length > 0) {
          isValid=false
          this.selectedDataValidationErrors[el]=true;
        } else {
          isValid=this.selectedData[el]
          this.selectedDataValidationErrors[el]=!this.selectedData[el];
        }
        if(this.selectedDataValidationErrors[el]){
          setTimeout(()=>{this.selectedDataValidationErrors[el]=!this.selectedDataValidationErrors[el]},2000)
        }
      })
      if (isValid) {
        this.edit = false
        this.$http.post(`/update/${this.componentId.replace('-','/')}/${this.type}`,this.selectedData).then(response => {
          this.selectedData.id = response.body.id
        });
      }
      return isValid
    },
    goPrint(printData){
      let equipmentsLength = printData.equipments.length >= 20 ? printData.equipments.length : 20
      let pageCSS = '<link rel="stylesheet" href="/static/styles/print-template.css" type="text/css" />';
      let printWindow = window.open("","","left=50,top=50,width=800, height=600,toolbar=0,scrollbars=1,status=0");
      let equipmentRows = ''
      for(let i=0; i<=equipmentsLength;i++){
        let isValid = printData.equipments[i] != undefined && printData.equipments[i] != 'undefined'
        equipmentRows+=`<tr><td>${i+1}</td><td>${isValid ? printData.equipments[i].name : ''}</td><td>${isValid ? printData.equipments[i].quantity+'.' : ''} </td><td>${isValid ? this.modeNumber(printData.equipments[i].price) : ''}</td><td>${isValid ? this.modeNumber(printData.equipments[i].total) : ''}</td></tr>`
      }
      printWindow.document.write(pageCSS);
      printWindow.document.write(`<div id="header-mini">  &#8243;&#8243;</div>
				<div style="height:20px"></div>
				<div id="header">  ${printData.id}  ${this.modeDate(printData.date,'DD.MM.YYYY')}</div>
				<div style="height:10px"></div>
				<div style="float:left; width:49%;"><b>:</b>   ${printData.reason}</div>
				<div style="float:right; width:49%;"><b>:</b> ${ this.getListName(this.departmentList,printData.department) }</div>
				<div style="height:10px; clear:both;"></div>
				<table class="data-user">
          <tr><th></th><th></th><th></th><th>  </th><th></th></tr>
          ${equipmentRows}
          <tr><td></td><td style="font-weight:bold; font-size:14px;"></td><td>${printData.total_count} .</td><td></td><td>${this.modeNumber(printData.total_sum)}</td></tr>
	  		</table>
				<div style="height:20px"></div>
				<div class="post"><i>:</i> ${ this.getListName(this.jobtitleList,printData.released_jobtitle) }____________${printData.released_fio}</div>
				<div style="height:20px"></div>
				<div class="post"><i>:</i>${ this.getListName(this.jobtitleList,printData.received_jobtitle) }____________${printData.received_fio}</div>
				<div style="height:20px"></div>`);
      printWindow.document.close();
      printWindow.onload = () => {
        printWindow.focus();
        printWindow.print();
        printWindow.close();
      };
    },
    handlerPrint() {
      let data = this.$refs.refComponent.$children[0].$children[0].store.states.currentRow;
      if(data && !this.dialogVisible){
        if(data.isItem){
          this.goPrint(data)
        }
      } else {
        if(!this.edit){
          this.goPrint(this.selectedData)
        }
      }
    }
  }
};
</script>

<style>

</style>



