<template>
  <div class="standart-tab directorys-topics__main pa-2" v-loading="loading">
    <vd-standart-tab-toolbar @btn-click="handlerBtnClick" v-model="filterText"></vd-standart-tab-toolbar>
    <vd-standart-tab-table ref="refComponent" @dbl-click="handlerEditClick" :filter-text="filterText" :main="toTree" :data="$vdesk.filterTable(filterText,listData)" ></vd-standart-tab-table>
    
    <v-dialog v-model="dialogVisible" max-width="500px" :overlay="true" scrollable>
      <v-card tile>
        <vd-standart-tab-dialog-toolbar v-model="dialogVisible" @btn-click="saveDataAndClose"></vd-standart-tab-dialog-toolbar>
        <v-card-text class="h-100">
          <el-form size="small" ref="form" :model="selectedData" label-width="150px">
            <el-form-item label="">
              <el-input :error="selectedDataValidationErrors.name" v-model="selectedData.name"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input-number v-model="selectedData.orders"></el-input-number>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="selectedData.visibility"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="selectedData.icon"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input type="textarea" :rows="4" v-model="selectedData.description"></el-input>
            </el-form-item>
          </el-form>
        </v-card-text>        
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "directorys-topics",
  props:{
    componentId:String
  },
  data(){
    return {
      toTree:{
        defSort:{prop: 'id', order: 'ascending'},
        key:'id',
        columns:[
          {prop:'id', label:'',width:80},
          {prop:'name', label:'',width:200},
          {prop:'orders', label:'',width:100},
          {prop:'visibility', label:'',width:120, render:(scope) => scope.row.visibility ? '' : ''},
          {prop:'icon', label:'',width:120},
          {prop:'description', label:''}
        ]
      },
      filterText:'',
      dialogVisible:false,
      selectedDataValidationErrors:{
        name:false
      },
      selectedData:{
        id:null,
        name:'',
        orders:null,
        visibility:null,
        icon:null,
        description:null
      },
      type:'new',
      loading:false
    }
  },
  computed: mapState({
      listData: state => state.directorys.topics
  }),
  methods:{
    handlerBtnClick(type){
      switch(type){
        case 'add':
          this.handlerAddClick()
          break;
        case 'edit':
          this.handlerEditClick()
          break;
        case 'delete':
          this.handlerDeleteClick()
          break;
      }
    },
    handlerDeleteClick(){
      let node = this.$refs.refComponent.$children[0].$children[0].store.states.currentRow;
      if(node){
        this.$confirm(`    - ${node.name}. ?`, 'Warning', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.selectedData = {id:node.id};
          this.type="delete"
          this.$vdesk.sendUpdate(this)
          this.$message({type: 'success',message: ' '});
        }).catch(() => {
          this.$message({type: 'info',message: ' '});
        });
      }      
    },
    handlerEditClick(row=this.$refs.refComponent.$children[0].$children[0].store.states.currentRow){
      if(row){
        this.selectedData = Object.assign({},row)
        this.type="update"
        this.dialogVisible=true
      } 
    },
    handlerAddClick() {
      this.selectedData = {id:null,name:''}
      this.type="new"
      this.dialogVisible = true
    },
    saveDataAndClose() {
      let isValid = true
      Object.keys(this.selectedDataValidationErrors).forEach( el => {
        if(!this.selectedData[el]){
          isValid = false
          this.selectedDataValidationErrors[el]=true;
          setTimeout(()=>{this.selectedDataValidationErrors[el]=false},2000)
        }
      })
      if(isValid){
        this.$vdesk.sendUpdate(this)
      }
    }
  }
};
</script>

<style>

</style>


