<template>
  <v-toolbar :class="toolbarsClasses" light dense>
    <v-text-field single-line hide-details class="mx-3 mt-2" label=" ..." v-model="filters.filterInput" :append-icon="filters.filterInput ? 'clear' : 'search'" :append-icon-cb="() => (filters.filterInput='')"></v-text-field>
    <v-spacer v-if="view!=='left'"></v-spacer>
    <div :slot="view==='left' ? 'extension' : $slots.default" class="mx-auto">
      <v-menu v-model="menus.statuses" class="" offset-y :close-on-content-click="false">
        <div slot="activator">
          
          <v-icon v-show="!menus.statuses">mdi-chevron-down</v-icon>
          <v-icon v-show="menus.statuses">mdi-chevron-up</v-icon>
        </div>
        <v-list dense>
          <v-list-tile>
            <v-list-tile-action>
              <v-checkbox hide-details :indeterminate="filters.statuses.isIndeterminate" v-model="filters.statuses.checkAll" @change="handlerStatusesCheckAllChange"></v-checkbox>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title></v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-list-tile v-for="statuse in filters.statuses.list" :key="statuse.id" >
            <v-list-tile-action>
              <v-checkbox color="red" @change="handlerCheckedStatusesChange" hide-details :value="statuse.id" v-model="filters.statuses.checkList"></v-checkbox>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>{{statuse.title}}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-menu>
      <v-menu v-model="menus.addons" offset-y :close-on-content-click="false">
        <div slot="activator">
          . 
          <v-icon v-show="!menus.addons">mdi-chevron-down</v-icon>
          <v-icon v-show="menus.addons">mdi-chevron-up</v-icon>
        </div>
        <v-list dense>            
          <v-list-tile v-for="item in filters.additionalOptions.list" :key="item.id" @click="handlerCheckedAdditionalOptionsChange(item.id)">
            <v-list-tile-title>
                <v-icon v-if="filters.additionalOptions.checkList===item.id">radio_button_checked</v-icon>
                <v-icon v-else>radio_button_unchecked</v-icon>
                {{item.title}}
              </v-list-tile-title>
            </v-list-tile>
          </v-list>          
      </v-menu>
      <v-menu v-model="menus.dates" offset-y :close-on-content-click="false">        
        <div slot="activator">
          
          <v-icon v-show="!menus.dates">mdi-chevron-down</v-icon>
          <v-icon v-show="menus.dates">mdi-chevron-up</v-icon>
        </div>

        <v-card>
          <table class="w-100">
            <tr>
              <td class="pa-3">
                <v-tooltip left>
                  <v-icon class="teal--text text--darken-2" slot="activator">mdi-alarm-plus</v-icon>
                  <span class="capital">{{ $locale({i: 'tikets.dateReceipt'}) }}</span>
                </v-tooltip>
              </td>
              <td class="pa-2">
                <el-date-picker popper-class="tikets-filter-date-picker" v-model="filters.dates.startdate" :format="filters.dates.format" :type="filters.dates.type" :picker-options="filters.dates.pickerOptions" placeholder="  "></el-date-picker>                
              </td>
            </tr>
            <tr>
              <td class="pa-3">
                <v-tooltip left>
                  <v-icon class="teal--text text--darken-2" slot="activator">mdi-alarm-off</v-icon>
                  <span class="capital">{{ $locale({i: 'tikets.deadlineCompletion'}) }}</span>
                </v-tooltip>
              </td>
              <td class="pa-2">
                <el-date-picker popper-class="tikets-filter-date-picker" v-model="filters.dates.deadline" :format="filters.dates.format" :type="filters.dates.type" :picker-options="filters.dates.pickerOptions" placeholder=" "></el-date-picker>
              </td>
            </tr>
            <tr>
              <td class="pa-3">
                <v-tooltip left>
                  <v-icon class="teal--text text--darken-2" slot="activator">mdi-alarm-check</v-icon>
                  <span class="capital">{{ $locale({i: 'tikets.endDate'}) }}</span>
                </v-tooltip>
              </td>
              <td class="pa-2">
                <el-date-picker popper-class="tikets-filter-date-picker" v-model="filters.dates.enddate" :format="filters.dates.format" :type="filters.dates.type" :picker-options="filters.dates.pickerOptions" placeholder="  "></el-date-picker>
              </td>
            </tr>
          </table>
        </v-card>          
      </v-menu>
      <el-dropdown v-model="menus.sort" v-if="view==='left'" class="tikets-sorter" @visible-change="menus.sort = !menus.sort" :hide-on-click="false" trigger="click" @command="handlerSort" placement='bottom-start'>
        <span class="el-dropdown-link">
          
          <v-icon v-show="!menus.sort">mdi-chevron-down</v-icon>
          <v-icon v-show="menus.sort">mdi-chevron-up</v-icon>
        </span>        
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item v-for="sortItem in sortingList" :command="sortItem.id" :key="sortItem.id" divided>
            <i :class="['el-icon--left',!!sortingData[sortItem.id] ? (sortingData[sortItem.id]===-1 ? 'el-icon-caret-top' : 'el-icon-caret-bottom' )   : 'el-icon-d-caret']"></i>
            {{sortItem.title}}
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </v-toolbar>
</template>

<script>
export default {
  name: 'tikets-list-fast-filter',
  props: {    
    view:String,
    value:Object,
    statuses:{
      type:Array,
      default:function(){
        return [3, 5, 6]
      }
    }
  },
  computed: {
    filters(){
      return this.value
    },
    toolbarsClasses(){
      return this.$store.state.maintabs.toolbarsClasses
    }
  },
  data() {
    return {
      menus:{
        dates:false,
        statuses:false,
        addons:false,
        sort:false
      },
      sortingData: {},
      sortingDataTemp: {},
      sortingList: [
        { title: ' ', id: 'id' },
        { title: '', id: 'sender' },
        { title: ' ', id: 'topicname' },
        { title: ' ', id: 'deadline' },
        { title: '', id: 'importance' },
        { title: '', id: 'status' },
        { title: '', id: 'new' },
        { title: '', id: 'attachment' },
        { title: '', id: 'description' },
        { title: '', id: 'dep' }
      ]
    }
  },
  methods: {    
    handlerSort(command) {
      if (!!!this.sortingDataTemp[command]) {
        this.sortingDataTemp[command] = -1;
      } else if (this.sortingDataTemp[command] === -1) {
        this.sortingDataTemp[command] = 1;
      } else {
        delete this.sortingDataTemp[command];
      }
      this.sortingData = Object.assign({}, this.sortingDataTemp);
      this.$emit('input', this.filters)
    },
    handlerStatusesCheckAllChange(event) {
      let array = [];
      let obj = this.filters.statuses;
      if (event/*.target.checked*/) {
        obj.list.forEach((el, idx) => {
          array.push(el.id);
        })
      }
      obj.checkList = array;
      obj.isIndeterminate = false;
      this.$emit('input', this.filters)
    },
    handlerCheckedStatusesChange(value) {
      let checkedCount = value.length;
      let obj = this.filters.statuses;
      obj.checkAll = checkedCount === obj.list.length;
      obj.isIndeterminate = checkedCount > 0 && checkedCount < obj.list.length;
      this.$emit('input', this.filters)
    },
    handlerCheckedAdditionalOptionsChange(value) {
      this.filters.additionalOptions.checkList=value
      this.$emit('input', this.filters)
    }
  }
}
</script>

<style>

</style>



