<template>
  <section id="main-layout">
    <header>
      <slot name="header"></slot>
    </header>
    <nav :style="{background:checkColor}">
      <slot name="toolbar"></slot>
    </nav>
    <aside :style="overflow">
      <slot name="aside"></slot>
    </aside>
    <main>
      <slot name="main"></slot>
    </main>
    <footer :style="{background:checkColor}">
      <slot name="footer"></slot>
    </footer>
  </section>
</template>

<script>
export default {
  name: 'layout',
  computed: {
    checkColor() {
      return this.$store.getters.getColor;
    },
    overflow() {
      return [
        this.$store.getters.sideIsCollapsed ? { 'overflow-y': 'visible' } : { 'overflow-y': 'auto' }
      ]
    }
  }
}
</script>

<style scoped>
section#main-layout {
  height: 100%;
  max-width: 100%;
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-template-rows: min-content min-content auto min-content;
  grid-template-areas: "header header" "toolbar toolbar" "aside main" "footer footer";
}

section#main-layout>header {
  grid-area: header;
}

section#main-layout>nav {
  grid-area: toolbar;
}

section#main-layout>aside {
  grid-area: aside;
}

section#main-layout>main {
  grid-area: main;
  max-height: 100%;
  max-width: 100%;
  overflow: hidden;
}

section#main-layout>footer {
  grid-area: footer;
}
</style>



