import moment from 'moment';
export default {
  state: {
    list_tikets: [],
    archive:null,
    viewList: [{
        name: '',
        type: 'left',
        icon: 'view_quilt'
      },
      {
        name: '',
        type: 'top',
        icon: 'view_stream'
      },
      {
        name: ' ',
        type: 'none',
        icon: 'view_headline'
      }
    ],
    view: 'left'
  },
  actions: {
    setTiketsList(ctx, list) {
      ctx.commit('SET_TIKETS_LIST', list)
    },
    addTiketsInList(ctx, data) {
      ctx.commit('ADD_TIKETS_IN_LIST', data)
    },
    addTiketLog(ctx, obj) {
      ctx.commit('ADD_TIKET_LOG', obj)
    },
    updateTiketsInList(ctx, obj) {
      ctx.commit('UPDATE_TIKETS_IN_LIST', obj)
    },
    deleteTiketsFromList(ctx, obj) {
      ctx.commit('DELETE_TIKETS_FROM_LIST', obj)
    },
    editTiketData(ctx, obj) {
      ctx.commit('EDIT_TIKET_DATA', obj)
    },
    editTiketShowData({commit,getters}, {data,type}) {
      /*commit('EDIT_TIKETS_SHOW_DATA', {
        getters,
        data,
        type
      })*/
    },
    printTiket(ctx, data) {
      var enddate = '';
      var workertext = '';
      if (data.enddate) {
        enddate = moment(data.enddate).format('DD.MM.YYYY HH:mm:ss');
      }
      if (!!data.equipments) {
        var workersArray = [];
        workertext = '';
        data.worker = '<ol>';
        data.equipments.forEach((el, idx) => {
          if (!~workersArray.indexOf(el.worker)) {
            data.worker += '<li>' + el.workerfio + '</li>';
            workersArray.push(el.worker);
          }
        })
        data.worker += '</ol>';
      }
      var newWindow = window.open('', '', 'width=800, height=600');
      var document = newWindow.document.open();
      var pageContent =
        `<!DOCTYPE html>
         <html>
          <head>
            <meta charset="utf-8"/>
            <title></title>
            <style>
              body{text-align:justify; font-size:13px;}
              td:first-child{text-align:right; width:150px; font-weight:bold;vertical-align:top;}
              ol{margin:0px;padding-left:12px;}
            </style>
          </head>
          <body>
            <table>
              <tr><td>:</td><td>${data.id}</td></tr>
              <tr><td>:</td><td>${data.topicname}</td></tr>
              <tr><td>:</td><td>${data.tiketstatusname}</td></tr>
              <tr><td>:</td><td>${data.depname}</td></tr>
              <tr><td>:</td><td>${data.sendersurname} ${data.sendername} ${data.senderpatronymic}</td></tr>
              <tr><td>${workertext}:</td><td>${data.workerFioLong}</td></tr>
              <tr><td> :</td><td>${moment(data.startdate).format('DD.MM.YYYY HH:mm:ss')}</td></tr>
              <tr><td> :</td><td>${moment(data.deadline).format('DD.MM.YYYY HH:mm:ss')}</td></tr>
              <tr><td> :</td><td>${enddate}</td></tr>
              <tr><td>:</td><td>${data.description}</td></tr>
            </table>
          </body>
        </html>`;
      document.write(pageContent);
      document.close();
      newWindow.print();
    }
  },
  mutations: {
    SET_TIKETS_LIST(state, {item:data,type}) {
      if(type==='archive'){
        state.archive = data.count        
      } else {
        state.list_tikets = data;
      }      
    },
    ADD_TIKETS_IN_LIST(state, data) {
      state.list_tikets.push(data);
    },
    UPDATE_TIKETS_IN_LIST(state, data) {
      let dataKeys=Object.keys(data)
      let idd=''
      state.list_tikets.forEach((el,idx)=>{
        if(el.id===data.id){
          Object.assign(el,data)
          idd = idx
          /*dataKeys.forEach((key)=>{
            if(key!=='logs' && key!=='id' && key!=='confirmity'){
              el[key]=data[key]
            } else if(key==='logs' && key!=='id' && key!=='confirmity') {
              Array.prototype.push.apply(el[key], data[key]);
            } else if(key!=='logs' && key!=='id' && key==='confirmity') {
              el[key].forEach((cEl,idx) => {
                if(cEl.id===data[key].id) el[key].splice(idx, 1, data[key])
              })
            }
          })*/
        }
      })
    },
    DELETE_TIKETS_FROM_LIST(state, id) {
      if (state.list_tikets.length > 0) {
        let idx = state.list_tikets.findIndex( elem => elem.id === id );
        state.list_tikets.splice(idx, 1);
      }
    },
    EDIT_TIKET_DATA(state, {field,value,type,id}) {
      state.list_tikets.forEach((el) => {
        if (el.id === id) {
          el[field] = value;
        }
      })
    },
    ADD_TIKET_LOG(state, {id,log,type}) {
      if (state.list_tikets.length > 0) {
        let list = state.list_tikets
        list.forEach((el, idx) => {
          if (el.id === id) {
            el.logs.push(log);
            return false;
          }
        })
      }
    },
    EDIT_TIKETS_SHOW_DATA(state, {getters,data,type}) {
      if (data.msg) {
        data.data.logs = data.msg
      }
      state.list_tikets.forEach((el) => {        
        if (el.id === data.data.logs.tiket) {
          el.logs.push(data.data.logs)
        }
      })
    }
  },
  getters: {
    getTiketsList(state, getters) {
      return (type) => {
        console.log(state.list_tikets)
        let login = getters.getWorkerLogin;
        let data;
        switch(type){
          case 'my':
            data = state.list_tikets.filter((el) => {
              if(el.worker && el.worker === login && !el.archive){
                return true
              }
            })
            break
          case 'archive':
            data = state.list_tikets.filter( el => el.archive )
            break
          default:
            data = state.list_tikets.filter( el => !el.archive )
            break
        }
        return data;
      }
    },
    filterTiketsToField(state, getters) {
      return (field, value, type) => {
        let list=getters.getTiketsList(type);
        return list.filter(el => {
          let isValid = false;
          if (Array.isArray(value)) {
            for (let i = 0; i < value.length; i++) {
              switch (typeof el[field]) {
                case 'number':
                  if (el[field] === value[i]) {
                    isValid = true;
                  }
                  break;
                default:
                  if (el[field].includes(value[i])) {
                    isValid = true;
                  }
                  bresk;
              }
            }
          } else {
            switch (typeof el[field]) {
              case 'number':
                isValid = el[field] === value[i];
                break;
              default:
                isValid = el[field].includes(value);
                bresk;
            }

          }
          return isValid;
        })
      }
    },
    getTiketsToId(state, getters) {
      return (id, type) => {
        let list=getters.getTiketsList(type)
        return list.filter(el => {
          let isValid = false;
          if (Array.isArray(id)) {
            for (let i = 0; i < id.length; i++) {
              if (el.id.endsWith(id[i])) {
                isValid = true;
              }
            }
          } else {
            isValid = el.id.endsWith(id);
          }
          return isValid;
        })
      }
    },
    getTiketsShow(state, getters) {
      return (type, id) => {
        let list=getters.getTiketsList(type)
        return list.find((el) => {
          return el.id === id
        })
      }
    },
    getTiketsShowView(state) {
      return state.view;
    },
    getTiketsShowViewList(state) {
      return state.viewList;
    },
    getTiketsCountses(state){
      let returned = {
        done:0,
        denied:0,
        fulfilling:0,
      }
      if(state.list_tikets.length>0){
        returned.done = state.list_tikets.reduce( (previousValue=0, currentValue, index, array) => {
            return currentValue.status === 1 ? previousValue + 1 : previousValue 
        },0)
        returned.denied = state.list_tikets.reduce( (previousValue=0, currentValue, index, array) => {
            return currentValue.status === 2 ? previousValue + 1 : previousValue 
        },0)
        returned.fulfilling = state.list_tikets.reduce( (previousValue=0, currentValue, index, array) => {
            return currentValue.status === 3 ? previousValue + 1 : previousValue 
        },0)
      }
      return returned
    }
  }
}



